import * as types from "../actions/types"

export const initialState = {
    name: "",
    deadline_time: null,
    deadline_round: null,
    agents: [],
    domains: [],
    loggers: [],
    estimators: [],

    self_negotiation: false,
    repeat: 1,
    result_dir: 'results/',
    seed: 1234,
    shuffle: false,
    drawing_format: 'plotly'
};

const configReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.UPDATE_AGENTS:
            return {
                ...state,
                agents: action.payload
            };
        case types.UPDATE_OPPONENT_MODELS:
            return {
                ...state,
                estimators: action.payload
            };
        case types.UPDATE_LOGGERS:
            return {
                ...state,
                loggers: action.payload
            };
        case types.UPDATE_DOMAINS:
            return {
                ...state,
                domains: action.payload
            };
        case types.UPDATE_SETTINGS:
            return {
                ...state,
                name: action.payload["name"],
                deadline_time: action.payload["deadline_time"],
                deadline_round: action.payload["deadline_round"],
                self_negotiation: action.payload["self_negotiation"],
                repeat: action.payload["repeat"],
                result_dir: action.payload["result_dir"],
                seed: action.payload["seed"],
                shuffle: action.payload["shuffle"],
                drawing_format: action.payload["drawing_format"],
            };
        case types.SET_CONFIG:
            return {
                agents: action.payload["agents"],
                estimators: action.payload["estimators"],
                loggers: action.payload["loggers"],
                domains: action.payload["domains"],
                name: action.payload["name"],
                deadline_time: action.payload["deadline_time"],
                deadline_round: action.payload["deadline_round"],
                self_negotiation: action.payload["self_negotiation"],
                repeat: action.payload["repeat"],
                result_dir: action.payload["result_dir"],
                seed: action.payload["seed"],
                shuffle: action.payload["shuffle"],
                drawing_format: action.payload["drawing_format"],
            };
        default:
            return {
                ...state
            };
    }
};

export default configReducer;
