import {useEffect, useState} from "react";


export const FloatType = "FloatType";
export const IntegerType = "IntegerType";
export const StringType = "StringType";

export const MinValue = "MinValue";
export const MaxValue = "MaxValue";


export const CheckMessage = ({checkList, name, minMaxType = ''}) => {
    const [text, setText] = useState("");

    useEffect(() => {
        if (checkList.hasOwnProperty(name + minMaxType)) {
            setText(checkList[name + minMaxType]);
        } else {
            setText("");
        }

    }, [checkList, name, minMaxType]);

    return (
        <>
            {text !== '' && <small className="text-danger px-2" id={name + minMaxType}>{text}</small>}
        </>

    );
};


export const checkForMinMaxType = (e, value, setValue, checkList, setCheckList, name, minMaxType, valueType = FloatType) => {
    let newCheckList = {...checkList, [name + minMaxType]: ""};

    const setNewCheckList = (candidate) => {
        newCheckList = {...candidate};
    };

    if (!checkForType(e, (v) => {minMaxType === MinValue ? setValue([v, value[1]]) : setValue([value[0], v])},
        newCheckList, setNewCheckList, name + minMaxType, valueType)) {

        setCheckList(newCheckList);

        return false;
    }

    const val = valueType === FloatType ? parseFloat(e.target.value) : parseInt(e.target.value);

    if (minMaxType === MaxValue) {
        if (val < value[0]) {
            setNewCheckList({...newCheckList, [name + minMaxType]: "Max. value must be greater"});

        } else {
            setNewCheckList({...newCheckList, [name + minMaxType]: ""});
            if (minMaxType === MinValue)
                setNewCheckList({...newCheckList, [name + MaxValue]: ""});
            else
                setNewCheckList({...newCheckList, [name + MinValue]: ""});
        }
    } else {
        if (val > value[1]) {
            setNewCheckList({...newCheckList, [name + minMaxType]: "Min. value must be lower"});

        } else {
            setNewCheckList({...newCheckList, [name + minMaxType]: ""});
            if (minMaxType === MinValue)
                setNewCheckList({...newCheckList, [name + MaxValue]: ""});
            else
                setNewCheckList({...newCheckList, [name + MinValue]: ""});
        }
    }

    setCheckList(newCheckList);

};



const checkForType = (e, setValue, checkList, setCheckList, name, valueType) => {
    setValue(e.target.value.toString());

    const val = e.target.value.toString();

    if (val === '') {
        setCheckList({...checkList, [name]: "You need to fill this field."});

        return false;
    }

    try {
        if (valueType === FloatType) {
            const value = parseFloat(val);

            if (isNaN(value)) {
                setCheckList({...checkList, [name]: "You need to fill this field properly."});

                return false;
            }

            if (value < 0 || value > 1) {
                setCheckList({...checkList, [name]: "Value must be in range [0.0, 1.0]"});

                return false;
            }

            setCheckList({...checkList, [name]: ""});

            return true;
        } else if (valueType === IntegerType) {
            const value = parseInt(val);

            if (isNaN(value)) {
                setCheckList({...checkList, [name]: "You need to fill this field properly."});

                return false;
            }

            if (parseFloat(val) !== value || val.includes(".")) {
                setCheckList({...checkList, [name]: "You need to enter an integer."});

                return false;
            }

            if (value <= 0) {
                setCheckList({...checkList, [name]: "Value must be a positive integer."});

                return false;
            }

            setCheckList({...checkList, [name]: ""});

            return true;
        } else {
            setCheckList({...checkList, [name]: ""});

            return true;
        }

    } catch (e) {
        setCheckList({...checkList, [name]: "You must enter a number."});

        return false;
    }
};


export const getCheckStyle = (checkList, name, minMaxType = '') => {
    if (checkList.hasOwnProperty(name + minMaxType) && checkList[name + minMaxType] !== '') {
        return "form-control border-danger border-2";
    } else {
        return "form-control";
    }
};


export default checkForType;