import {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {DragDropContext, Draggable, Droppable} from "react-beautiful-dnd";
import {updateLoggers} from "../../../actions/config";
import {doubleClickedMove, getSelectionStyle, move, reorder} from "./utils";
import axios from "axios";
import InfoModal, {MODAL_ERROR} from "../../modals/InfoModal";
import {SERVER_ADDRESS, CLIENT_ADDRESS} from "../../../config";

const logger_list = {
    "EstimatorMetricLogger": "Estimation Performance Metrics (round by round)",
    "EstimatorOnlyFinalMetricLogger": "Estimation Performance Metrics (only final)",
    "EstimatedParetoLogger": "Pareto Estimation Performance Evaluation"
};


const checkLogger = async (path) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/check/logger_path",
            JSON.stringify({path: path}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const LoggersPage = ({setPage}) => {
    const [loggerMap, setLoggerMap] = useState({});

    const [remainingLoggers, setRemainingLoggers] = useState([]);
    const [loggers, setLoggers] = useState([]);

    const [selectedLoggers, setSelectedLoggers] = useState([]);
    const [selectedPool, setSelectedPool] = useState([]);

    const [loggerPath, setLoggerPath] = useState("");

    const [disabledAdd, setDisabledAdd] = useState(true);
    const [disabledAll, setDisabledAll] = useState(false);
    const [disabledAddAll, setDisabledAddAll] = useState(false);
    const [disabledRemoveAll, setDisabledRemoveAll] = useState(true);

    const [selectDefault, setSelectDefault] = useState(true);
    const [selectGraph, setSelectGraph] = useState(false);
    const [selectOppModel, setSelectOppModel] = useState(false);

    const [hoverDefault, setHoverDefault] = useState(false);
    const [hoverGraph, setHoverGraph] = useState(false);
    const [hoverOppModel, setHoverOppModel] = useState(false);

    const [errorModal, setErrorModal] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    const configLoggers = useSelector(state => state.config.loggers);

    const dispatch = useDispatch();

    const handleLoggerPath = (e) => {
        setLoggerPath(e.target.value);

        if (e.target.value.length === 0) {
            setDisabledAdd(true);
        } else {
            setDisabledAdd(false);
        }
    };

    const handleAdd = async () => {
        setDisabledAdd(true);
        let checkForDuplicate = false;

        for (const [key, value] of Object.entries(loggerMap)) {
            if (value === loggerPath) {
                checkForDuplicate = true;
                break;
            }
        }

        if (checkForDuplicate) {
            setErrorMessage("Logger already exists!");
            setErrorModal(true);

            setDisabledAdd(false);
        }
        if (!disabledAdd && loggerPath !== '' && !loggers.includes(loggerPath) && !remainingLoggers.includes(loggerPath)) {
            const response = await checkLogger(loggerPath);

            if (response.error) {
                setErrorMessage(response.errorMessage);
                setErrorModal(true);

                setDisabledAdd(loggerPath === '');
            } else {
                let newLoggers = [...loggers];

                newLoggers.push(loggerPath);

                let newLoggerMap = {...loggerMap};

                newLoggerMap[response["class_name"]] = response["path"];

                setLoggers(newLoggers);
                setLoggerMap(newLoggerMap);
                setLoggerPath("");
                setDisabledAdd(true);
            }
        }
    };

    const handleKeyUp = (e) => {
        if (e.key === 'Enter')
            handleAdd();
    };

    const handleSubmit = () => {
        if (!disabledAll) {
            setDisabledAll(true);
            setDisabledAdd(true);
            setDisabledAddAll(true);
            setDisabledRemoveAll(true);

            let fullLoggers = [...loggers];
            if (selectDefault)
                fullLoggers.push("DefaultLoggers");
            if (selectGraph)
                fullLoggers.push("GraphLoggers");
            if (selectOppModel)
                fullLoggers.push("OppModelLoggers");

            dispatch(updateLoggers(fullLoggers));

            if (selectOppModel)
                setPage("OPPONENT_MODELS");
            else
                setPage("TOURNAMENT_SETTINGS");
        }
    };

    const goBack = () => {
        if (!disabledAll) {
            setDisabledAll(true);
            setDisabledAdd(true);
            setDisabledAddAll(true);
            setDisabledRemoveAll(true);

            let fullLoggers = [...loggers];
            if (selectDefault)
                fullLoggers.push("DefaultLoggers");
            if (selectGraph)
                fullLoggers.push("GraphLoggers");
            if (selectOppModel)
                fullLoggers.push("OppModelLoggers");

            dispatch(updateLoggers(fullLoggers));

            setPage("DOMAINS");
        }

    };

    const handleAddAll = () => {
        let newLoggers = [...loggers];
        let newRemainingLoggers = [];

        for (let i in remainingLoggers) {
            if (selectedPool.length === 0 || selectedPool.includes(parseInt(i))) {
                newLoggers.push(remainingLoggers[i]);
            } else if (selectedPool.length > 0 && !selectedPool.includes(parseInt(i))) {
                newRemainingLoggers.push(remainingLoggers[i]);
            }
        }

        if (selectedPool.length === 0) {
            setSelectDefault(true);
            setSelectGraph(true);
            setSelectOppModel(true);
        }

        setRemainingLoggers(newRemainingLoggers);
        setSelectedPool([]);
        setLoggers(newLoggers);
    };

    const handleRemoveAll = () => {
        let newRemainingLoggers = [...remainingLoggers];
        let newLoggers = [];

        for (let i in loggers) {
            if (selectedLoggers.length === 0 || selectedLoggers.includes(parseInt(i))) {
                newRemainingLoggers.push(loggers[i]);
            } else if (selectedLoggers.length > 0 && !selectedLoggers.includes(parseInt(i))) {
                newLoggers.push(loggers[i]);
            }
        }

        if (selectedLoggers.length === 0) {
            setSelectDefault(false);
            setSelectGraph(false);
            setSelectOppModel(false);
        }

        setSelectedLoggers([]);
        setRemainingLoggers(newRemainingLoggers);
        setLoggers(newLoggers);
    };

    const onDragEnd = (result) => {
        const {source, destination} = result;

        if (!destination) {
            return;
        }

        if (source.droppableId === destination.droppableId) {
            const items = reorder(
                source.droppableId === "Selected" ? loggers : remainingLoggers,
                source.index,
                destination.index
            );

            if (source.droppableId === 'Selected') {
                setLoggers(items);
                setSelectedLoggers([]);
            } else {
                setRemainingLoggers(items);
                setSelectedPool([]);
            }
        } else {
            const result = move(
                source.droppableId === "Selected" ? loggers : remainingLoggers,
                destination.droppableId === "Selected" ? loggers : remainingLoggers,
                source,
                destination
            );

            setLoggers(result["Selected"]);
            setRemainingLoggers(result["Others"]);

            if (source.droppableId === 'Selected')
                setSelectedLoggers([]);
            else
                setSelectedPool([]);
        }
    };

    const onClickToSelect = (index, selectionList, setSelectionList) => {
        if (selectionList.includes(index)) {
            let newSelectedDomains = [...selectionList];

            const i = newSelectedDomains.indexOf(index);

            newSelectedDomains.splice(i, 1);

            setSelectionList(newSelectedDomains);
        } else {
            let newSelectedDomains = [...selectionList];

            newSelectedDomains.push(index);

            setSelectionList(newSelectedDomains);
        }
    };

    const onDoubleClick = (index, sourceList, destinationList, setSourceList, setDestinationList) => {
        const result = doubleClickedMove(index, sourceList, destinationList);

        setSourceList(result["Source"]);
        setDestinationList(result["Target"]);

        setSelectedLoggers([]);
        setSelectedPool([]);
    };

    const onMouseEnter = (name) => {
        switch (name) {
            case "Default":
                setHoverDefault(true);
                setHoverGraph(false);
                setHoverOppModel(false);
                break;
            case "Graph":
                setHoverDefault(false);
                setHoverGraph(true);
                setHoverOppModel(false);
                break;
            case "OppModel":
                setHoverDefault(false);
                setHoverGraph(false);
                setHoverOppModel(true);
                break;
            default:
                setHoverDefault(false);
                setHoverGraph(false);
                setHoverOppModel(false);
                break;
        }
    };

    const getName = (path) => {
        if (logger_list.hasOwnProperty(path)) {
            return logger_list[path];
        }

        for (const [key, value] of Object.entries(loggerMap)) {
            if (value === path)
                return key;
        }

        return path;
    };

    useEffect(() => {
        let newLoggers = [...configLoggers];

        if (configLoggers.includes("DefaultLoggers")) {
            setSelectDefault(true);

            newLoggers.splice(newLoggers.indexOf("DefaultLoggers", 1));
        }

        if (configLoggers.includes("GraphLoggers")) {
            setSelectGraph(true);

            newLoggers.splice(newLoggers.indexOf("GraphLoggers"), 1);
        }

        if (configLoggers.includes("OppModelLoggers")) {
            setSelectOppModel(true);

            newLoggers.splice(newLoggers.indexOf("OppModelLoggers"), 1);
        }

        setLoggers(newLoggers);

        let newPool = [];

        for (const [key, value] of Object.entries(logger_list)) {
            if (!newLoggers.includes(key)) {
                newPool.push(key);
            }
        }

        newPool.sort();

        setRemainingLoggers(newPool);

    }, [configLoggers]);

    useEffect(() => {
        setDisabledAddAll(selectedPool.length === 0 && remainingLoggers.length === 0 && selectDefault && selectGraph && selectOppModel);
    }, [selectedPool, remainingLoggers, selectDefault, selectGraph, selectOppModel]);

    useEffect(() => {
        setDisabledRemoveAll(selectedLoggers.length === 0 && loggers.length === 0 && !selectDefault && !selectGraph && !selectOppModel);

        if (loggers.includes("EstimatorMetricLogger") || loggers.includes("EstimatorOnlyFinalMetricLogger") || loggers.includes("EstimatedParetoLogger")) {
            setSelectOppModel(true);
        }

    }, [selectedLoggers, loggers, selectDefault, selectGraph, selectOppModel]);

    useEffect(() => {
        if (!selectOppModel) {
            let newLoggers = [...loggers];

            if (loggers.includes("EstimatorMetricLogger")) {
                newLoggers.splice(newLoggers.indexOf("EstimatorMetricLogger"), 1);
                remainingLoggers.push("EstimatorMetricLogger");
            }

            if (loggers.includes("EstimatorOnlyFinalMetricLogger")) {
                newLoggers.splice(newLoggers.indexOf("EstimatorOnlyFinalMetricLogger"), 1);
                remainingLoggers.push("EstimatorOnlyFinalMetricLogger");
            }

            if (loggers.includes("EstimatedParetoLogger")) {
                newLoggers.splice(newLoggers.indexOf("EstimatedParetoLogger"), 1);
                remainingLoggers.push("EstimatedParetoLogger");
            }

            setLoggers(newLoggers);
        }
    }, [selectOppModel]);

    return (
        <div className="col">
            <div className="row" style={{height: "15vh"}}>
                <div className="d-flex justify-content-center align-items-center">
                    <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <span className="input-group-text" id="label_logger_path">Logger Path</span>
                        </div>
                        <input type="text" className="form-control" placeholder="path.to.logger.LoggerClass"
                               aria-label="LoggerPath" aria-describedby="label_logger_path"
                               value={loggerPath}
                               onInput={handleLoggerPath} onKeyUp={handleKeyUp}/>
                        <button className="btn btn-outline-primary rounded mx-1" onClick={handleAdd}
                                disabled={disabledAdd}>Add
                        </button>
                    </div>
                </div>
            </div>
            <div className="row overflow-scroll" style={{height: "75vh"}}>
                <div className="d-flex justify-content-between">
                    <DragDropContext onDragEnd={onDragEnd}>
                        <Droppable droppableId={"Others"} key={"Others"}>
                            {(provided, snapshot) => (
                                <div
                                    className="col-5 rounded border border-2 bg-white overflow-scroll shadow-sm" {...provided.droppableProps}
                                    ref={provided.innerRef} style={{height: "70vh"}}>
                                    <div className="row m-1 p-1 mb-2 border-bottom border-1">
                                        <div className="d-inline-flex justify-content-center">
                                            <b>Logger Types</b>
                                        </div>
                                    </div>
                                    <div className="row m-1 p-1 border-bottom border-1">
                                        <div className="d-inline-flex justify-content-start"
                                             onMouseEnter={() => onMouseEnter("Default")} onMouseLeave={() => setHoverDefault(false)}>
                                            <input type="checkbox" className="form-check-input mx-1 border-2" checked={selectDefault} onChange={() => setSelectDefault(!selectDefault)} disabled={disabledAll}/>
                                            <b>Default Loggers</b>
                                        </div>
                                        {hoverDefault &&
                                            <div className="row px-1 m-2">
                                                <ul className="list-group list-group-numbered">
                                                    <li className="list-group-item text-muted">Move Analyze Logger</li>
                                                    <li className="list-group-item text-muted">Bid Space Logger</li>
                                                    <li className="list-group-item text-muted">Tournament Summary Logger</li>
                                                </ul>
                                            </div>
                                        }
                                    </div>
                                    <div className="row m-1 p-1 border-bottom border-1">
                                        <div className="d-inline-flex justify-content-start"
                                             onMouseEnter={() => onMouseEnter("Graph")} onMouseLeave={() => setHoverGraph(false)}>
                                            <input type="checkbox" className="form-check-input mx-1 border-2" checked={selectGraph} onChange={() => setSelectGraph(!selectGraph)} disabled={disabledAll}/>
                                            <b>Graph Loggers</b>
                                        </div>
                                        {hoverGraph &&
                                            <div className="row px-1 m-2">
                                                <ul className="list-group list-group-numbered">
                                                    <li className="list-group-item text-muted">Final Graphs Logger</li>
                                                    <li className="list-group-item text-muted">Domain Graphs Logger</li>
                                                    <li className="list-group-item text-muted">Utility Distribution Logger</li>
                                                </ul>
                                            </div>
                                        }
                                    </div>
                                    <div className="row m-1 p-1 border-bottom border-1">
                                        <div className="d-inline-flex justify-content-start"
                                             onMouseEnter={() => onMouseEnter("OppModel")} onMouseLeave={() => setHoverOppModel(false)}>
                                            <input type="checkbox" className="form-check-input mx-1 border-2" checked={selectOppModel} onChange={() => setSelectOppModel(!selectOppModel)} disabled={disabledAll}/>
                                            <b>Estimation Loggers (acc. opp. model)</b>
                                        </div>
                                        {hoverOppModel &&
                                            <div className="row px-1 m-2">
                                                <ul className="list-group list-group-numbered">
                                                    <li className="list-group-item text-muted">Estimated Utility Logger</li>
                                                    <li className="list-group-item text-muted">Estimated Move Logger</li>
                                                    <li className="list-group-item text-muted">Estimated Bid Space Logger</li>
                                                </ul>
                                            </div>
                                        }
                                    </div>
                                    {remainingLoggers.length === 0 && <span className="m-1">No remaining logger.</span>}
                                    {remainingLoggers.length > 0 && remainingLoggers.map((loggerName, index) => (
                                        <Draggable key={loggerName} draggableId={loggerName}
                                                   index={index}>
                                            {(provided, snapshot) => (
                                                <div className={getSelectionStyle(index, selectedPool)}
                                                     ref={provided.innerRef}
                                                     onClick={() => onClickToSelect(index, selectedPool, setSelectedPool)}
                                                     onDoubleClick={() => onDoubleClick(index, remainingLoggers, loggers, setRemainingLoggers, setLoggers)}
                                                     {...provided.draggableProps}
                                                     {...provided.dragHandleProps}
                                                     style={{...provided.draggableProps.style}}>
                                                    <span>{getName(loggerName)}</span>
                                                </div>
                                            )}
                                        </Draggable>
                                    ))}
                                    {provided.placeholder}
                                </div>
                            )}
                        </Droppable>
                        <div className="col-1" style={{height: "70vh"}}>
                            <div className="d-flex justify-content-between align-items-center" style={{height: "70vh"}}>
                                <button className="btn btn-outline-secondary rounded" onClick={handleAddAll} disabled={disabledAddAll}>{">>"}</button>
                                <button className="btn btn-outline-secondary rounded" onClick={handleRemoveAll} disabled={disabledRemoveAll}>{"<<"}</button>
                            </div>
                        </div>
                        <Droppable droppableId={"Selected"} key={"Selected"}>
                            {(provided, snapshot) => (
                                <div
                                    className="col-5 rounded border border-2 bg-white overflow-scroll shadow-sm" {...provided.droppableProps}
                                    ref={provided.innerRef} style={{height: "70vh"}}>
                                    <div className="row m-1 p-1 mb-2 border-bottom border-1">
                                        <div className="d-inline-flex justify-content-center">
                                            <b>Selected Loggers</b>
                                        </div>
                                    </div>
                                    {selectDefault &&
                                    <div className="row m-1 p-1 border-bottom border-1">
                                        <div className="d-inline-flex justify-content-start">
                                            <b>Default Loggers</b>
                                        </div>
                                    </div>
                                    }
                                    {selectGraph &&
                                    <div className="row m-1 p-1 border-bottom border-1">
                                        <div className="d-inline-flex justify-content-start">
                                            <b>Graph Loggers</b>
                                        </div>
                                    </div>
                                    }
                                    {selectOppModel &&
                                    <div className="row m-1 p-1 border-bottom border-1">
                                        <div className="d-inline-flex justify-content-start">
                                            <b>Estimation Loggers (acc. opp. model)</b>
                                        </div>
                                    </div>
                                    }
                                    {loggers.length === 0 && !selectDefault && !selectGraph && !selectOppModel &&
                                        <span className="m-1">No logger has been selected yet.</span>}
                                    {loggers.length > 0 && loggers.map((loggerName, index) => (
                                        <Draggable key={loggerName} draggableId={loggerName}
                                                   index={index}>
                                            {(provided, snapshot) => (
                                                <div className={getSelectionStyle(index, selectedLoggers)}
                                                     ref={provided.innerRef}
                                                     onClick={() => onClickToSelect(index, selectedLoggers, setSelectedLoggers)}
                                                     onDoubleClick={() => onDoubleClick(index, loggers, remainingLoggers, setLoggers, setRemainingLoggers)}
                                                     {...provided.draggableProps}
                                                     {...provided.dragHandleProps}
                                                     style={{...provided.draggableProps.style}}>
                                                    <span>{getName(loggerName)}</span>
                                                </div>
                                            )}
                                        </Draggable>
                                    ))}
                                    {provided.placeholder}
                                </div>
                            )}
                        </Droppable>
                    </DragDropContext>
                </div>
            </div>
            <div className="row my-2 mx-1 bg-secondary p-1">
                <div className="d-flex justify-content-between">
                    <button className="btn btn-light rounded" onClick={goBack} disabled={disabledAll}>Back
                    </button>
                    <button className="btn btn-primary rounded" onClick={handleSubmit} disabled={disabledAll}>Next
                    </button>
                </div>
            </div>
            {errorModal && <InfoModal modalType={MODAL_ERROR} setModal={setErrorModal} message={errorMessage}/>}
        </div>
    );
};

export default LoggersPage;
