import {useEffect, useState} from "react";
import {editDomain, setDomain, setDomains as SetDomains, showDomain} from "../../actions/domainGenerator";
import {useDispatch, useSelector} from "react-redux";
import axios from "axios";
import InfoModal, {MODAL_ERROR, MODAL_INFO, MODAL_SUCCESS} from "../modals/InfoModal";
import {SERVER_ADDRESS, CLIENT_ADDRESS} from "../../config";


const fetchDomains = async () => {
    try {
        const response = await axios.get(SERVER_ADDRESS + "/fetch/domains",
            {
            headers: {'Content-Type': 'multipart/form-data', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const removeDomains = async (selectedDomains) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/remove/domains",
            JSON.stringify({domainNames: selectedDomains}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const openFolder = async (path) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/open/folder",
            JSON.stringify({path: path}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


export const DomainRow = ({domain, index, selectedDomains, setSelectedDomains}) => {
    const [className, setClassName] = useState("");

    useEffect(() => {
        if (selectedDomains.includes(index)) {
            setClassName("table-primary");
        } else {
            setClassName("");
        }
    }, [selectedDomains, index]);

    const onClickToSelect = (index) => {
        if (selectedDomains.includes(index)) {
            let newSelectedDomains = [...selectedDomains];

            const i = newSelectedDomains.indexOf(index);

            newSelectedDomains.splice(i, 1);

            setSelectedDomains(newSelectedDomains);
        } else {
            let newSelectedDomains = [...selectedDomains];

            newSelectedDomains.push(index);

            setSelectedDomains(newSelectedDomains);
        }
    };

    return (
        <tr onClick={() => onClickToSelect(index)} className={className}>
            <th scope="row" className="text-center">{domain["DomainName"]}</th>
            <td className="text-center">{domain["DomainSize"]}</td>
            <td className="text-center">[{domain["IssueValues"].toString()}]</td>
            <td className="text-center">{domain["Opposition"]}</td>
            <td className="text-center">{parseFloat(domain["BalanceScore"]) === 0 ? 0 : domain["BalanceScore"]}</td>
        </tr>
    );
};


const DomainsPage = ({setPage}) => {
    const [domains, setDomains] = useState(null);
    const [selectedDomains, setSelectedDomains] = useState([]);

    const [sortType, setSortType] = useState([null, null]);

    const [disabledAll, setDisabledAll] = useState(false);
    const [disabledEdit, setDisabledEdit] = useState(true);
    const [disabledRemove, setDisabledRemove] = useState(true);

    const [errorModal, setErrorModal] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    const [successModal, setSuccessModal] = useState(false);
    const [successMessage, setSuccessMessage] = useState("");

    const [removeModal, setRemoveModal] = useState(false);
    const [removeModalMessage, setRemoveModalMessage] = useState("");

    const generatedDomains = useSelector(state => state.domainGenerator.domains);

    const dispatch = useDispatch();

    const goBack = () => {
        dispatch(SetDomains([]));
        setPage("MAIN");
    };

    const onClickToSort = (colName) => {
        if (sortType[0] === colName) {
            if (sortType[1] === 'DESC')
                setSortType([colName, 'ASC']);
            else
                setSortType([colName, 'DESC']);
        } else {
            setSortType([colName, 'ASC']);
        }
    };

    const onClickToRemove = () => {
        if (!disabledAll && !disabledRemove) {
            setRemoveModal(true);
            setRemoveModalMessage(selectedDomains.length > 1 ?
                "Are you sure to remove the domains?" :
                "Are you sure to remove the domain?");
        }
    };

    const onClickToUseAsTemplate = () => {
        if (!disabledAll && !disabledEdit) {
            dispatch(editDomain(domains[selectedDomains[0]]));
            setPage("CREATE_DOMAIN");
        }
    };

    const onClickToEdit = () => {
        if (!disabledAll && !disabledEdit) {
            dispatch(setDomain(domains[selectedDomains[0]]["DomainName"].substring(6), false));
            setPage("EDIT_DOMAIN");
        }
    };

    const onClickToOpen = async () => {
        if (!disabledAll && !disabledEdit) {
            const response = await openFolder("domains/" + domains[selectedDomains[0]]["DomainName"]);

            if (response.error) {
                setErrorMessage(response.errorMessage);
                setErrorModal(true);
            }
        }
    };

    const onClickToShow = () => {
        if (!disabledAll && !disabledEdit) {
            dispatch(showDomain(domains[selectedDomains[0]]["DomainName"]));

            setPage("SHOW_DOMAIN");
        }
    };

    const removeDomain = async () => {
        if (!disabledAll && !disabledRemove) {
            setRemoveModal(false);
            setDisabledAll(true);

            let targetDomains = [];

            for (let i in selectedDomains) {
                targetDomains.push(domains[selectedDomains[i]]["RealName"])
            }

            const response = await removeDomains(targetDomains);

            if (response.error) {
                setErrorMessage(response.errorMessage);
                setErrorModal(true);
            } else {
                setSuccessMessage("Selected domains are successfully removed.");

                setSuccessModal(true);
            }

            setSelectedDomains([]);

            setDisabledAll(false);
        }
    };

    const closeSuccessModal = () => {
        setSuccessModal(false);
        dispatch(SetDomains([]));
    };

    useEffect(() => {
        if (generatedDomains.length === 0) {
            fetchDomains().then((response) => {
                if (response.error) {
                    setErrorModal(true);
                    setErrorMessage(response.errorMessage);
                } else {
                    dispatch(SetDomains(response["domains"]));
                }
            });
        } else {
            setDomains(generatedDomains);
        }
    }, [generatedDomains]);

    useEffect(() => {
        if (sortType[0] !== null && sortType[1] !== null) {
            let sortedDomains = [...domains];

            if (sortType[0] !== 'IssueValues')
                sortedDomains.sort((a, b) => (a[sortType[0]] < b[sortType[0]] ? -1 : 1))
            else
                sortedDomains.sort((a, b) => (a["IssueValues"].length < b["IssueValues"].length ? -1 : 1));

            if (sortType[1] === 'DESC') {
                sortedDomains.reverse();
            }

            let sortedSelectedDomains = [];

            for (let i in selectedDomains) {
                const domain = domains[selectedDomains[i]];

                sortedSelectedDomains.push(sortedDomains.indexOf(domain));
            }

            setDomains(sortedDomains);
            setSelectedDomains(sortedSelectedDomains);
        }
    }, [sortType]);

    useEffect(() => {
        if (selectedDomains.length !== 1)
            setDisabledEdit(true);
        else
            setDisabledEdit(false);

        if (selectedDomains.length > 0)
            setDisabledRemove(false);
        else
            setDisabledRemove(true);
    }, [selectedDomains]);

    return (
        <>
            <div className="container my-2 py-2">
                <div className="row bg-secondary" style={{height: "10vh"}}>
                    <div className="d-flex justify-content-between align-items-center">
                        <button className="btn btn-primary rounded" disabled={disabledAll || disabledEdit}
                                onClick={() => onClickToUseAsTemplate()}>Use as Template</button>
                        <button className="btn btn-light rounded" disabled={disabledAll || disabledEdit}
                                onClick={() => onClickToOpen()}>Open Folder</button>
                        <button className="btn btn-light rounded" disabled={disabledAll || disabledEdit}
                                onClick={() => onClickToShow()}>Show</button>
                        <button className="btn btn-light rounded" disabled={disabledAll || disabledEdit}
                                onClick={() => onClickToEdit()}>Edit</button>
                        <button className="btn btn-danger rounded" disabled={disabledAll || disabledRemove}
                                onClick={() => onClickToRemove()}>Remove
                        </button>
                    </div>
                </div>
                <div className="row my-2 mx-1 table-responsive" style={{height: "75vh"}}>
                    <table className="table table-striped table-hover border border-1 shadow-sm">
                        <thead className="sticky-top">
                        <tr>
                            <th scope="col" onClick={() => onClickToSort("DomainName")} className="text-center">Domain
                                Name {sortType[0] === 'DomainName' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("DomainSize")} className="text-center">Domain
                                Size {sortType[0] === 'DomainSize' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("IssueValues")} className="text-center">Issue
                                Values {sortType[0] === 'IssueValues' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("Opposition")}
                                className="text-center">Opposition {sortType[0] === 'Opposition' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("BalanceScore")}
                                className="text-center">Balance
                                Score {sortType[0] === 'BalanceScore' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                        </tr>
                        </thead>
                        <tbody>
                        {domains === null && <h3 className="m-1"><b>Loading...</b></h3>}
                        {domains !== null && domains.length === 0 && <b className="m-1">No domain exist.</b>}
                        {domains !== null && domains.length > 0 && domains.map((domain, index) => (
                            <DomainRow domain={domain} index={index} selectedDomains={selectedDomains}
                                       setSelectedDomains={setSelectedDomains} key={index}/>
                        ))}
                        </tbody>
                    </table>
                </div>
            </div>
            <div className="row my-2 mx-1 bg-secondary p-1">
                <div className="d-flex justify-content-start">
                    <button className="btn btn-light rounded" onClick={goBack}>Home Page
                    </button>
                </div>
            </div>
            {errorModal && <InfoModal modalType={MODAL_ERROR} setModal={setErrorModal} message={errorMessage}/>}
            {successModal && <InfoModal modalType={MODAL_SUCCESS} setModal={setSuccessModal} message={successMessage}
                                        closeHandler={closeSuccessModal} />}
            {removeModal &&
                <InfoModal modalType={MODAL_INFO} setModal={setRemoveModal} message={removeModalMessage} buttons={[{
                    "className": "btn btn-danger rounded",
                    "disabled": false,
                    "onClick": removeDomain,
                    "name": "Remove"
                }]}/>}
        </>

    );
};

export default DomainsPage;
