import {useState} from "react";
import AgentsPage from "./components/tournament/create/Agents";
import OpponentModelsPage from "./components/tournament/create/OpponentModels";
import LoggersPage from "./components/tournament/create/Loggers";
import DomainsPage from "./components/tournament/create/Domains";
import TournamentSettingsPage from "./components/tournament/create/TournamentSettings";
import MainPage from "./components/MainPage";
import DomainGeneratorPage from "./components/domain/DomainGeneratorPage";
import DomainDisplayPage from "./components/domain/DomainDisplayPage";
import RunTournamentsPage from "./components/tournament/RunTournaments";
import MonitorTournamentsPage from "./components/tournament/MonitorTournaments";
import SingleDomainDisplayPage from "./components/domain/SingleDomainDisplayPage";
import ManualGeneratorPage from "./components/domain/ManualGenerator";
import SingleDomainEditPage from "./components/domain/SingleDomainEditPage";

function App() {
  const [page, setPage] = useState("MAIN");

  return (
    <div className="bg-dark vh-100 p-2">
      <div className="bg-light container-fluid position-relative">
        {page === 'MAIN' && <MainPage setPage={setPage}/>}
        {page === 'AGENTS' && <AgentsPage setPage={setPage}/>}
        {page === 'OPPONENT_MODELS' && <OpponentModelsPage setPage={setPage}/>}
        {page === 'LOGGERS' && <LoggersPage setPage={setPage}/>}
        {page === 'DOMAINS' && <DomainsPage setPage={setPage}/>}
        {page === 'TOURNAMENT_SETTINGS' && <TournamentSettingsPage setPage={setPage}/>}
        {page === 'CREATE_DOMAINS' && <DomainGeneratorPage setPage={setPage}/>}
        {page === 'CREATE_DOMAIN' && <ManualGeneratorPage setPage={setPage}/>}
        {page === 'EDIT_DOMAIN' && <SingleDomainEditPage setPage={setPage}/>}
        {page === 'DISPLAY_DOMAINS' && <DomainDisplayPage setPage={setPage} />}
        {page === 'SHOW_DOMAIN' && <SingleDomainDisplayPage setPage={setPage} />}
        {page === 'RUN_TOURNAMENTS' && <RunTournamentsPage setPage={setPage} /> }
        {page === 'MONITOR_TOURNAMENTS' && <MonitorTournamentsPage setPage={setPage} /> }
      </div>
    </div>
  );
}

export default App;
