export const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
};

export const move = (source, destination, droppableSource, droppableDestination) => {
    const sourceClone = Array.from(source);
    const destClone = Array.from(destination);
    const [removed] = sourceClone.splice(droppableSource.index, 1);

    destClone.splice(droppableDestination.index, 0, removed);

    const result = {};
    result[droppableSource.droppableId] = sourceClone;
    result[droppableDestination.droppableId] = destClone;

    return result;
};


export const doubleClickedMove = (index, source, target) => {
    const sourceClone = Array.from(source);
    const targetClone = Array.from(target);

    const [removed] = sourceClone.splice(index, 1);

    targetClone.push(removed);

    const result = {};
    result["Source"] = sourceClone;
    result["Target"] = targetClone;

    return result;
};


export const getSelectionStyle = (index, selection) => {
        if (selection.includes(index)) {
            return "row m-1 p-1 border border-primary border-1";
        } else {
            return "row m-1 p-1";
        }
};