import * as types from "./types"

export const setDomains = (domains) => {
    return {
        type: types.SET_DOMAINS,
        payload: domains
    };
};


export const setDomain = (domain, remove = false) => {
    return {
        type: types.SET_DOMAIN,
        payload: {domain: domain, remove: remove}
    };
};


export const editDomain = (domain) => {
    return {
        type: types.EDIT_DOMAIN,
        payload: domain
    };
};

export const showDomain = (domain) => {
    return {
        type: types.SHOW_DOMAIN,
        payload: domain
    };
};
