import {useDispatch, useSelector} from "react-redux";
import {useEffect, useState} from "react";
import axios from "axios";
import InfoModal, {MODAL_ERROR, MODAL_SUCCESS} from "../modals/InfoModal";
import EditReservationValueModal from "../modals/EditReservationValueModal";
import EditIssueModal from "../modals/EditIssueModal";
import EditValueModal from "../modals/EditValueModal";
import {setDomain, setDomains} from "../../actions/domainGenerator";
import {SERVER_ADDRESS, CLIENT_ADDRESS} from "../../config";


const fetchBidSpace = async (domain) => {
    try {
        const response = await axios.get(SERVER_ADDRESS + "/fetch/bid_space/domain" + domain,
            {
                headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data[0].file;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const fetchProfiles = async (selectedDomain) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/fetch/profiles",
            JSON.stringify({name: "domain" + selectedDomain}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const removeDomain = async (domainName) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/remove/domains",
            JSON.stringify({domainNames: [domainName]}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};

const submitDomain = async (name, profileA, profileB, save) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/edit/domain",
            JSON.stringify({
                name: name,
                profileA: profileA,
                profileB: profileB,
                save: save
            }), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const ProfileEditor = ({profile, setProfile, otherProfile, setOtherProfile, disabled, setDisabled}) => {
    const [reservationValueModal, setReservationValueModal] = useState(false);

    const [issueModal, setIssueModal] = useState(false);
    const [selectedIssue, setSelectedIssue] = useState(null);

    const [valueModal, setValueModal] = useState(false);
    const [selectedValue, setSelectedValue] = useState(null);

    const onClickForReservationValue = () => {
        if (!disabled && !reservationValueModal && !issueModal && !valueModal) {
            setDisabled(true)
            setReservationValueModal(true);
        }
    };

    const onClickForIssue = (issue) => {
        if (!disabled && !reservationValueModal && !issueModal && !valueModal) {
            setDisabled(true);
            setSelectedIssue(issue);
            setIssueModal(true);
        }
    };

    const onClickForValue = (issue, value) => {
        if (!disabled && !reservationValueModal && !issueModal && !valueModal) {
            setDisabled(true);

            setSelectedIssue(issue);
            setSelectedValue(value);

            setValueModal(true);
        }
    };

    useEffect(() => {
        if (!reservationValueModal && !issueModal && !valueModal) {
            setDisabled(false);
        }

    }, [reservationValueModal, issueModal, valueModal]);

    return (
        <>
            <div className="row m-1 p-1">
                <div className="d-inline-flex" style={{cursor: "text"}} onDoubleClick={onClickForReservationValue}>
                    <span className="text-danger"><b>reservationValue:</b></span><span className="mx-1 text-muted">{profile["reservationValue"]}</span>
                </div>
            </div>
            <div className="row m-1 p-1">
                <span className="text-danger"><b>issueWeights:</b></span>
                {Object.entries(profile["issueWeights"]).map(([key, value]) => (
                    <div className="d-inline-flex" key={key} style={{cursor: "text"}} onDoubleClick={() => onClickForIssue(key)}>
                        <span className="mx-1">{key}:</span><span className="mx-1 text-muted">{Math.round(value * 1000) / 1000}</span></div>
                ))}
            </div>
            <div className="row m-1 p-1">
                <span className="text-danger"><b>issues:</b></span>
                {Object.entries(profile["issues"]).map(([issueName, values]) => (
                    <div className="row m-1" key={issueName + "Utilities"}>
                        <span className="mx-1 text-danger">{issueName}:</span>
                        {Object.entries(values).map(([valueName, utility]) => (
                            <div className="d-inline-flex" key={valueName} style={{cursor: "text"}} onDoubleClick={() => onClickForValue(issueName, valueName)}>
                                <span className="mx-2">{valueName}:</span><span className="mx-2 text-muted">{Math.round(utility * 1000) / 1000}</span></div>
                        ))}
                    </div>
                ))}
            </div>
            {reservationValueModal && <EditReservationValueModal profile={profile} setProfile={setProfile} setModal={setReservationValueModal} /> }
            {issueModal && <EditIssueModal profile={profile} setProfile={setProfile} otherProfile={otherProfile} setOtherProfile={setOtherProfile} setModal={setIssueModal} issue={selectedIssue}/> }
            {valueModal && <EditValueModal profile={profile} setProfile={setProfile} otherProfile={otherProfile} setOtherProfile={setOtherProfile} setModal={setValueModal} issueName={selectedIssue} value={selectedValue} /> }
        </>
    );
};


const SingleDomainEditPage = ({setPage}) => {
    const [bidSpace, setBidSpace] = useState(null);
    const [profileA, setProfileA] = useState(null);
    const [profileB, setProfileB] = useState(null);
    const [opposition, setOpposition] = useState(null);
    const [numberOfBids, setNumberOfBids] = useState(null);
    const [balanceScore, setBalanceScore] = useState(null);

    const selectedDomain = useSelector(state => state.domainGenerator.domain);
    const removeOnCancel = useSelector(state => state.domainGenerator.removeOnCancel);

    const [errorModal, setErrorModal] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    const [successModal, setSuccessModal] = useState(false);
    const [successMessage, setSuccessMessage] = useState("");

    const [disabled, setDisabled] = useState(false);

    const [updated, setUpdated] = useState(false);

    const dispatch = useDispatch();

    const goBack = async () => {
        if (!disabled) {
            setDisabled(true);

            if (removeOnCancel) {
                const response = await removeDomain(selectedDomain);

                if (response.error) {
                    setErrorMessage(response.errorMessage);
                    setErrorModal(true);

                    setDisabled(false);

                    return;
                }

                dispatch(setDomain(null));

                setPage("MAIN");
            } else {
                dispatch(setDomain(null));

                setPage("DISPLAY_DOMAINS");
            }
        }
    };

    const onClickToUpdate = async () => {
        if (updated && !disabled) {
            setDisabled(true);

            const response = await submitDomain(selectedDomain, profileA, profileB, false);

            if (response.error) {
                setErrorMessage(response.errorMessage);
                setErrorModal(response.error);
            } else {
                setBidSpace(null);
                setUpdated(false);
            }

            setDisabled(false);
        }
    };

    const onClickToCreate = async () => {
        if (!updated && !disabled) {
            setDisabled(true);

            const response = await submitDomain(selectedDomain, profileA, profileB, true);

            if (response.error) {
                setErrorMessage(response.errorMessage);
                setErrorModal(response.error);

                setDisabled(false);
            } else {
                setUpdated(false);

                dispatch(setDomains(response["domains"]));

                setSuccessModal(true);
                setSuccessMessage("Domain is successfully generated.");
            }
        }
    };

    const closeSuccessModal = () => {
        setSuccessModal(false);

        dispatch(setDomain(null));

        setPage("DISPLAY_DOMAINS");
    };

    useEffect(() => {
        if (selectedDomain === null && !successModal && !disabled) {
            setPage("MAIN");
        }
    }, [selectedDomain, successModal]);

    useEffect(() => {
        if (bidSpace === null && selectedDomain !== null) {
            fetchProfiles(selectedDomain).then((response1) => {
                if (response1.error) {
                    setErrorMessage(response1.errorMessage);
                    setErrorModal(true);
                } else {
                    setProfileA(response1["profiles"]["ProfileA"]);
                    setProfileB(response1["profiles"]["ProfileB"]);
                    setBalanceScore(Math.round(response1["specs"]["BalanceScore"] * 1000) / 1000);
                    setOpposition(Math.round(response1["specs"]["Opposition"] * 1000) / 1000);
                    setNumberOfBids(response1["specs"]["NumberOfBids"]);

                    fetchBidSpace(selectedDomain).then((response2) => {
                        setBidSpace(response2);
                        setUpdated(false);
                    });
                }
            });

        }
    }, [bidSpace, selectedDomain]);

    useEffect(() => {
        setUpdated(true);
    }, [profileA, profileB]);

    return (
        <>
            <div className="row py-2 border border-1 rounded overflow-auto" style={{height: "90vh"}}>
                <div className="d-flex justify-content-center">
                    <div className="col-4 my-2 overflow-auto border border-1 rounded shadow-sm" style={{height: "90vh"}}>
                        <div className="row m-2 p-1 border-1 border-bottom">
                            <b>Profile A</b>
                        </div>
                        { profileA !== null && <ProfileEditor profile={profileA} setProfile={setProfileA} otherProfile={profileB} setOtherProfile={setProfileB} disabled={disabled} setDisabled={setDisabled} /> }
                    </div>
                    <div className="col-4 my-2 overflow-auto border border-1 rounded shadow-sm" style={{height: "90vh"}}>
                        <div className="row m-2 p-1 border-1 border-bottom">
                            <b>Profile B</b>
                        </div>
                        { profileB !== null && <ProfileEditor profile={profileB} setProfile={setProfileB} otherProfile={profileA} setOtherProfile={setProfileA} disabled={disabled} setDisabled={setDisabled} /> }
                    </div>
                    <div className="col-4 my-2 overflow-auto align-items-center shadow-sm">
                        <div className="row">
                            {bidSpace !== null && <img className="w-100" src={SERVER_ADDRESS + "/fetch/bid_space/domain" + selectedDomain} alt="Bid Space"/>}
                        </div>
                        {balanceScore !== null && opposition !== null && numberOfBids !== null &&
                            <>
                                <div className="row m-2 p-1 border-1 border-top border-bottom">
                                    <div className="d-inline-flex">
                                        <span className="text-danger"><b>Opposition:</b></span><span className="text-muted mx-2">{opposition}</span>
                                    </div>
                                </div>
                                <div className="row m-2 p-1 border-1 border-top border-bottom">
                                    <div className="d-inline-flex">
                                        <span className="text-danger"><b>Balance Score:</b></span><span className="text-muted mx-2">{balanceScore}</span>
                                    </div>
                                </div>
                                <div className="row m-2 p-1 border-1 border-top border-bottom">
                                    <div className="d-inline-flex">
                                        <span className="text-danger"><b>Number of Bids:</b></span><span className="text-muted mx-2">{numberOfBids}</span>
                                    </div>
                                </div>
                            </>
                        }
                    </div>
                </div>
            </div>
            <div className="row my-2 mx-1 bg-secondary p-1">
                <div className="d-flex justify-content-between">
                    <button className="btn btn-light rounded" onClick={goBack} disabled={disabled}>Back
                    </button>
                    {disabled && <h3><b>Please, wait...</b></h3>}
                    <div className="d-flex justify-content-between">
                        <button className="btn btn-success rounded mx-3" onClick={onClickToUpdate} disabled={disabled || !updated}>Update</button>
                        <button className="btn btn-primary rounded mx-3" onClick={onClickToCreate} disabled={disabled || updated}>Create</button>
                    </div>
                </div>
            </div>
            {errorModal && <InfoModal modalType={MODAL_ERROR} setModal={setErrorModal} message={errorMessage}/>}
            {successModal && <InfoModal modalType={MODAL_SUCCESS} setModal={setSuccessModal} message={successMessage}
                                        closeHandler={closeSuccessModal} />}
        </>
    );
};

export default SingleDomainEditPage;
