import {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {DragDropContext, Draggable, Droppable} from "react-beautiful-dnd";
import {updateOpponentModels} from "../../../actions/config";
import {doubleClickedMove, getSelectionStyle, reorder, move} from "./utils";
import axios from "axios";
import InfoModal, {MODAL_ERROR} from "../../modals/InfoModal";
import {SERVER_ADDRESS, CLIENT_ADDRESS} from "../../../config";


const opponent_models = [
    "ClassicFrequencyOpponentModel",
    "WindowedFrequencyOpponentModel",
    "BayesianOpponentModel",
    "ConflictBasedOpponentModel"
];


const checkOpponentModel = async (path) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/check/opp_model_path",
            JSON.stringify({path: path}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const OpponentModelsPage = ({setPage}) => {
    const [opponentModelMap, setOpponentModelMap] = useState({});

    const [remainingOpponentModels, setRemainingOpponentModels] = useState([...opponent_models]);
    const [opponentModels, setOpponentModels] = useState([]);

    const [selectedOpponentModels, setSelectedOpponentModels] = useState([]);
    const [selectedPool, setSelectedPool] = useState([]);

    const [opponentModelPath, setOpponentModelPath] = useState("");

    const [disabledAdd, setDisabledAdd] = useState(true);
    const [disabledAll, setDisabledAll] = useState(false);
    const [disabledAddAll, setDisabledAddAll] = useState(false);
    const [disabledRemoveAll, setDisabledRemoveAll] = useState(true);

    const configOpponentModels = useSelector(state => state.config.estimators);

    const [errorModal, setErrorModal] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    const dispatch = useDispatch();

    const handleOpponentModelPath = (e) => {
        setOpponentModelPath(e.target.value);

        if (e.target.value.length === 0) {
            setDisabledAdd(true);
        } else {
            setDisabledAdd(false);
        }
    };

    const handleAdd = async () => {
        setDisabledAdd(true);
        let checkForDuplicate = false;

        for (const [key, value] of Object.entries(opponentModelMap)) {
            if (value === opponentModelPath) {
                checkForDuplicate = true;
                break;
            }
        }

        if (checkForDuplicate) {
            setErrorMessage("Logger already exists!");
            setErrorModal(true);

            setDisabledAdd(false);
        }
        if (!disabledAdd && opponentModelPath !== '' && !opponentModels.includes(opponentModelPath) && !remainingOpponentModels.includes(opponentModelPath)) {
            const response = await checkOpponentModel(opponentModelMap);

            if (response.error) {
                setErrorMessage(response.errorMessage);
                setErrorModal(true);

                setDisabledAdd(opponentModelPath === '');
            } else {
                let newOpponentModels = [...opponentModels];

                newOpponentModels.push(opponentModelPath);

                let newOppModelMap = {...opponentModelMap};

                newOppModelMap[response["class_name"]] = response["path"];

                setOpponentModels(newOpponentModels);
                setOpponentModelMap(newOppModelMap);
                setOpponentModelPath("");
                setDisabledAdd(true);
            }
        }
    };

    const handleKeyUp = (e) => {
        if (e.key === 'Enter')
            handleAdd();
    };

    const handleSubmit = () => {
        if (!disabledAll) {
            setDisabledAddAll(true);
            setDisabledRemoveAll(true);
            setDisabledAll(true);
            setDisabledAdd(true);

            dispatch(updateOpponentModels(opponentModels));

            setPage("TOURNAMENT_SETTINGS");
        }
    };

    const goBack = () => {
        if (!disabledAll) {
            setDisabledAddAll(true);
            setDisabledRemoveAll(true);
            setDisabledAll(true);
            setDisabledAdd(true);

            dispatch(updateOpponentModels(opponentModels));

            setPage("LOGGERS");
        }
    };

    const handleAddAll = () => {
        let newOpponentModels = [...opponentModels];
        let newRemainingOpponentModels = [];

        for (let i in remainingOpponentModels) {
            if (selectedPool.length === 0 || selectedPool.includes(parseInt(i))) {
                newOpponentModels.push(remainingOpponentModels[i]);
            } else if (selectedPool.length > 0 && !selectedPool.includes(parseInt(i))) {
                newRemainingOpponentModels.push(remainingOpponentModels[i]);
            }
        }

        setRemainingOpponentModels(newRemainingOpponentModels);
        setSelectedPool([]);
        setOpponentModels(newOpponentModels);
    };

    const handleRemoveAll = () => {
        let newRemainingOpponentModels = [...remainingOpponentModels];
        let newOpponentModels = [];

        for (let i in opponentModels) {
            if (selectedOpponentModels.length === 0 || selectedOpponentModels.includes(parseInt(i))) {
                newRemainingOpponentModels.push(opponentModels[i]);
            } else if (selectedOpponentModels.length > 0 && !selectedOpponentModels.includes(parseInt(i))) {
                newOpponentModels.push(opponentModels[i]);
            }
        }

        setSelectedOpponentModels([]);
        setRemainingOpponentModels(newRemainingOpponentModels);
        setOpponentModels(newOpponentModels);
    };

    const onDragEnd = (result) => {
        const {source, destination} = result;

        if (!destination) {
            return;
        }

        if (source.droppableId === destination.droppableId) {
            const items = reorder(
                source.droppableId === "Selected" ? opponentModels : remainingOpponentModels,
                source.index,
                destination.index
            );

            if (source.droppableId === 'Selected') {
                setOpponentModels(items);
                setSelectedOpponentModels([]);
            } else {
                setRemainingOpponentModels(items);
                setSelectedPool([]);
            }
        } else {
            const result = move(
                source.droppableId === "Selected" ? opponentModels : remainingOpponentModels,
                destination.droppableId === "Selected" ? opponentModels : remainingOpponentModels,
                source,
                destination
            );

            setOpponentModels(result["Selected"]);
            setRemainingOpponentModels(result["Others"]);

            if (source.droppableId === 'Selected')
                setSelectedOpponentModels([]);
            else
                setSelectedPool([]);
        }
    };

    const onClickToSelect = (index, selectionList, setSelectionList) => {
        if (selectionList.includes(index)) {
            let newSelectedDomains = [...selectionList];

            const i = newSelectedDomains.indexOf(index);

            newSelectedDomains.splice(i, 1);

            setSelectionList(newSelectedDomains);
        } else {
            let newSelectedDomains = [...selectionList];

            newSelectedDomains.push(index);

            setSelectionList(newSelectedDomains);
        }
    };

    const onDoubleClick = (index, sourceList, destinationList, setSourceList, setDestinationList) => {
        const result = doubleClickedMove(index, sourceList, destinationList);

        setSourceList(result["Source"]);
        setDestinationList(result["Target"]);

        setSelectedOpponentModels([]);
        setSelectedPool([]);
    };

    const getName = (path) => {
        if (opponent_models.hasOwnProperty(path)) {
            return path;
        }

        for (const [key, value] of Object.entries(opponentModelMap)) {
            if (value === path)
                return key;
        }

        return path;
    };

    useEffect(() => {
        setOpponentModels(configOpponentModels);

        let newPool = [];

        opponent_models.sort();

        for (let i in opponent_models) {
            if (!configOpponentModels.includes(opponent_models[i])) {
                newPool.push(opponent_models[i]);
            }
        }

        setRemainingOpponentModels(newPool);

    }, [configOpponentModels]);

    useEffect(() => {
        setDisabledAddAll(selectedPool.length === 0 && remainingOpponentModels.length === 0);
    }, [selectedPool, remainingOpponentModels]);

    useEffect(() => {
        setDisabledRemoveAll(selectedOpponentModels.length === 0 && opponentModels.length === 0);
    }, [selectedOpponentModels, opponentModels]);

    return (
        <div className="col">
            <div className="row" style={{height: "15vh"}}>
                <div className="d-flex justify-content-center align-items-center">
                    <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <span className="input-group-text" id="label_opponent_model_path">Preference Estimator Path</span>
                        </div>
                        <input type="text" className="form-control" placeholder="path.to.estimator.OpponentModelClass"
                               aria-label="OpponentModelPath" aria-describedby="label_opponent_model_path"
                               value={opponentModelPath}
                               onInput={handleOpponentModelPath} onKeyUp={handleKeyUp}/>
                        <button className="btn btn-outline-primary rounded mx-1" onClick={handleAdd}
                                disabled={disabledAdd}>Add
                        </button>
                    </div>
                </div>
            </div>
            <div className="row overflow-hidden" style={{height: "75vh"}}>
                <div className="d-flex justify-content-between">
                    <DragDropContext onDragEnd={onDragEnd}>
                        <Droppable droppableId={"Others"} key={"Others"}>
                            {(provided, snapshot) => (
                                <div
                                    className="col-5 rounded border border-2 bg-white overflow-scroll shadow-sm" {...provided.droppableProps}
                                    ref={provided.innerRef} style={{height: "70vh"}}>
                                    <div className="row m-1 p-1 mb-2 border-bottom border-1">
                                        <div className="d-inline-flex justify-content-center">
                                            <b>Preference Estimator Pool</b>
                                        </div>
                                    </div>
                                    {remainingOpponentModels.length === 0 && <span className="m-1">No remaining opponent model.</span>}
                                    {remainingOpponentModels.length > 0 && remainingOpponentModels.map((opponentModelName, index) => (
                                        <Draggable key={opponentModelName} draggableId={opponentModelName}
                                                   index={index}>
                                            {(provided, snapshot) => (
                                                <div className={getSelectionStyle(index, selectedPool)}
                                                     ref={provided.innerRef}
                                                     onClick={() => onClickToSelect(index, selectedPool, setSelectedPool)}
                                                     onDoubleClick={() => onDoubleClick(index, remainingOpponentModels, opponentModels, setRemainingOpponentModels, setOpponentModels)}
                                                     {...provided.draggableProps}
                                                     {...provided.dragHandleProps}
                                                     style={{...provided.draggableProps.style}}>
                                                    <span>{getName(opponentModelName)}</span>
                                                </div>
                                            )}
                                        </Draggable>
                                    ))}
                                    {provided.placeholder}
                                </div>
                            )}
                        </Droppable>
                        <div className="col-1" style={{height: "70vh"}}>
                            <div className="d-flex justify-content-between align-items-center" style={{height: "70vh"}}>
                                <button className="btn btn-outline-secondary rounded" onClick={handleAddAll} disabled={disabledAddAll}>{">>"}</button>
                                <button className="btn btn-outline-secondary rounded" onClick={handleRemoveAll} disabled={disabledRemoveAll}>{"<<"}</button>
                            </div>
                        </div>
                        <Droppable droppableId={"Selected"} key={"Selected"}>
                            {(provided, snapshot) => (
                                <div
                                    className="col-5 rounded border border-2 bg-white overflow-scroll shadow-sm" {...provided.droppableProps}
                                    ref={provided.innerRef} style={{height: "70vh"}}>
                                    <div className="row m-1 p-1 mb-2 border-bottom border-1">
                                        <div className="d-inline-flex justify-content-center">
                                            <b>Selected Preference Estimator</b>
                                        </div>
                                    </div>
                                    {opponentModels.length === 0 &&
                                        <span className="m-1">No opponent model has been selected yet.</span>}
                                    {opponentModels.length > 0 && opponentModels.map((opponentModelName, index) => (
                                        <Draggable key={opponentModelName} draggableId={opponentModelName}
                                                   index={index}>
                                            {(provided, snapshot) => (
                                                <div className={getSelectionStyle(index, selectedOpponentModels)}
                                                     ref={provided.innerRef}
                                                     onClick={() => onClickToSelect(index, selectedOpponentModels, setSelectedOpponentModels)}
                                                     onDoubleClick={() => onDoubleClick(index, opponentModels, remainingOpponentModels, setOpponentModels, setRemainingOpponentModels)}
                                                     {...provided.draggableProps}
                                                     {...provided.dragHandleProps}
                                                     style={{...provided.draggableProps.style}}>
                                                    <span>{getName(opponentModelName)}</span>
                                                </div>
                                            )}
                                        </Draggable>
                                    ))}
                                    {provided.placeholder}
                                </div>
                            )}
                        </Droppable>
                    </DragDropContext>
                </div>
            </div>
            <div className="row my-2 mx-1 bg-secondary p-1">
                <div className="d-flex justify-content-between">
                    <button className="btn btn-light rounded" onClick={goBack} disabled={disabledAll}>Back
                    </button>
                    <button className="btn btn-primary rounded" onClick={handleSubmit} disabled={disabledAll}>Next
                    </button>
                </div>
            </div>
            {errorModal && <InfoModal modalType={MODAL_ERROR} setModal={setErrorModal} message={errorMessage}/>}
        </div>
    );
};

export default OpponentModelsPage;
