import {useEffect, useState} from "react";
import axios from "axios";
import InfoModal, {MODAL_ERROR, MODAL_SUCCESS} from "../modals/InfoModal";
import {useDispatch, useSelector} from "react-redux";
import {editDomain as EditDomain, setDomains} from "../../actions/domainGenerator";
import checkForType, {
    checkForMinMaxType, CheckMessage,
    FloatType, getCheckStyle,
    IntegerType,
    MaxValue,
    MinValue,
    StringType
} from "./DomainGeneratorCheck";
import {SERVER_ADDRESS, CLIENT_ADDRESS} from "../../config";


const submit = async (config) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/create/domains",
            JSON.stringify({config: config}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const DomainGeneratorPage = ({setPage}) => {
    const [numberOfDomains, setNumberOfDomains] = useState(1);
    const [name, setName] = useState("");

    const [issueSize, setIssueSize] = useState([3, 6]);
    const [valueSize, setValueSize] = useState([3, 6]);

    const [oppositionRange, setOppositionRange] = useState([0.0, 1.0]);

    const [balanceScoreRange, setBalanceScoreRange] = useState([0.0, 0.05]);

    const [reservationValueA, setReservationValueA] = useState(0.0);
    const [reservationValueB, setReservationValueB] = useState(0.0);

    const [utilityRange, setUtilityRange] = useState([0.0, 1.0]);

    const [hasRandomness, setHasRandomness] = useState(true);

    const [checkList, setCheckList] = useState({});

    const [disabledSubmit, setDisabledSubmit] = useState(true);
    const [disabledAll, setDisabledAll] = useState(false);

    const [errorModal, setErrorModal] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    const [successModal, setSuccessModal] = useState(false);
    const [successMessage, setSuccessMessage] = useState("");

    const editDomain = useSelector(state => state.domainGenerator.editDomain);

    const dispatch = useDispatch();

    const handleSubmit = async () => {
        setDisabledAll(true);
        setDisabledSubmit(true);

        const config = {
            numberOfDomains: numberOfDomains,
            name: name,
            issue_size_range: issueSize,
            value_size_range: valueSize,
            opposition_range: oppositionRange,
            balance_score_range: balanceScoreRange,
            reservation_value_profile_a: reservationValueA,
            reservation_value_profile_b: reservationValueB,
            utility_range: utilityRange,
            has_randomness: hasRandomness
        };

        const response = await submit(config);

        if (response.error) {
            setErrorModal(true);
            setErrorMessage(response.errorMessage);
        } else {
            dispatch(setDomains(response["domains"]));

            setSuccessModal(true);
            setSuccessMessage(numberOfDomains === 1 ? "Domain is successfully generated." : "Domains are successfully generated.");
        }

        setName("");
        setDisabledAll(false);
    };

    const closeSuccessModal = () => {
        dispatch(EditDomain(null));
        setSuccessModal(false);
        setPage("DISPLAY_DOMAINS");
    };

    const goBack = () => {
        dispatch(EditDomain(null));
        setPage("MAIN");
    };

    useEffect(() => {
        if (numberOfDomains <= 0 || (!name || name === '')) {
            setDisabledSubmit(true);
        } else if (isNaN(issueSize[0]) || isNaN(issueSize[1]) || isNaN(valueSize[0]) || isNaN(valueSize[1]) ||
            isNaN(oppositionRange[0]) || isNaN(oppositionRange[1]) || isNaN(balanceScoreRange[0]) ||
            isNaN(balanceScoreRange[1]) || isNaN(reservationValueA) || isNaN(reservationValueB) || isNaN(utilityRange[0]) ||
            isNaN(utilityRange[1]) || isNaN(numberOfDomains)) {
            setDisabledSubmit(true);
        } else {
            let check = true;

            for (const [key, value] of Object.entries(checkList)) {
                if (value !== '') {
                    check = false;

                    break;
                }
            }

            setDisabledSubmit(!check);
        }
    }, [numberOfDomains, name, issueSize, valueSize, oppositionRange, balanceScoreRange, reservationValueA, reservationValueB, utilityRange, checkList]);

    useEffect(() => {
        if (editDomain === null)
            return;

        setNumberOfDomains(1);
        setName(editDomain["RealName"]);

        setIssueSize([editDomain["IssueValues"].length, editDomain["IssueValues"].length]);

        let minValue = Math.min(...editDomain["IssueValues"]);
        let maxValue = Math.max(...editDomain["IssueValues"]);

        setValueSize([minValue, maxValue]);

        setBalanceScoreRange([Math.max(0., parseFloat(editDomain["BalanceScore"]) - 0.05), Math.min(1., parseFloat(editDomain["BalanceScore"]) + 0.05)]);
        setOppositionRange([Math.max(0., parseFloat(editDomain["Opposition"]) - 0.1), Math.min(1., parseFloat(editDomain["Opposition"]) + 0.1)]);

        setReservationValueA(parseFloat(editDomain["ReservationValueA"]));
        setReservationValueB(parseFloat(editDomain["ReservationValueB"]));

        setUtilityRange([parseFloat(editDomain["MinUtility"]), parseFloat(editDomain["MaxUtility"])]);

        setHasRandomness(true);

    }, [editDomain]);

    return (
        <div className="col">
            <div className="row overflow-auto" style={{height: "90vh"}}>
                <div className="row">
                    <div className="input-group my-2">
                        <div className="col-3 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text"><b>Number of Domains</b><small className="mx-1"> {"> 0"}</small></span>
                            </div>
                        </div>
                        <div className="col-4 mx-3">
                            <input type="text" className={getCheckStyle(checkList, "NumberOfDomains")} placeholder="1"
                                   value={numberOfDomains}
                                   onInput={(e) => checkForType(e, setNumberOfDomains, checkList, setCheckList, "NumberOfDomains", IntegerType)}
                                   disabled={disabledAll || editDomain !== null}/>

                            <CheckMessage name="NumberOfDomains" checkList={checkList} />
                        </div>

                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-2">
                        <div className="col-3 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text"><b>{numberOfDomains > 1 ? "Name" : "Name Format"}</b></span>
                            </div>
                        </div>
                        <div className="col-4 mx-3">
                            <input type="text" className={getCheckStyle(checkList, "Name")} placeholder="Example"
                                   value={name}
                                   onInput={(e) => checkForType(e, setName, checkList, setCheckList, "Name", StringType)}
                                   disabled={disabledAll || editDomain !== null}/>
                            <CheckMessage name="Name" checkList={checkList} />
                        </div>

                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-2">
                        <div className="col-3 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text"><b>Number of Issue Range</b><small className="mx-1"> {"> 0"}</small></span>
                            </div>
                        </div>
                        <div className="col-4 mx-3">
                            <div className="d-flex">
                                <div className="input-group-prepend w-100">
                                    <span className="input-group-text">Min</span>
                                </div>
                                <input type="text" className={getCheckStyle(checkList, "IssueSize", MinValue)} placeholder="3"
                                       value={issueSize[0]}
                                       onInput={(e) => checkForMinMaxType(e, issueSize, setIssueSize, checkList, setCheckList, "IssueSize", MinValue, IntegerType)}
                                       disabled={disabledAll}/>
                            </div>
                            <CheckMessage name="IssueSize" checkList={checkList} minMaxType={MinValue} />
                        </div>
                        <div className="col-4 mx-1">
                            <div className="d-flex">
                                <div className="input-group-prepend w-100">
                                    <span className="input-group-text">Max</span>
                                </div>
                                <input type="text" className={getCheckStyle(checkList, "IssueSize", MaxValue)} placeholder="6"
                                       value={issueSize[1]}
                                       onInput={(e) => checkForMinMaxType(e, issueSize, setIssueSize, checkList, setCheckList, "IssueSize", MaxValue, IntegerType)}
                                       disabled={disabledAll}/>
                            </div>
                            <CheckMessage name="IssueSize" checkList={checkList} minMaxType={MaxValue} />
                        </div>

                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-2">
                        <div className="col-3 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text"><b>Number of Value Range for Each Issue</b><small className="mx-1"> {"> 0"}</small></span>
                            </div>
                        </div>
                        <div className="col-4 mx-3">
                            <div className="d-flex">
                                <div className="input-group-prepend w-100">
                                    <span className="input-group-text">Min</span>
                                </div>
                                <input type="text" className={getCheckStyle(checkList, "ValueSize", MinValue)} placeholder="3"
                                       value={valueSize[0]}
                                       onInput={(e) => checkForMinMaxType(e, valueSize, setValueSize, checkList, setCheckList, "ValueSize", MinValue, IntegerType)}
                                       disabled={disabledAll}/>
                            </div>
                            <CheckMessage name="ValueSize" checkList={checkList} minMaxType={MinValue} />
                        </div>
                        <div className="col-4 mx-1">
                            <div className="d-flex">
                                <div className="input-group-prepend w-100">
                                    <span className="input-group-text">Max</span>
                                </div>
                                <input type="text" className={getCheckStyle(checkList, "ValueSize", MaxValue)} placeholder="6"
                                       value={valueSize[1]}
                                       onInput={(e) => checkForMinMaxType(e, valueSize, setValueSize, checkList, setCheckList, "ValueSize", MaxValue, IntegerType)}
                                       disabled={disabledAll}/>
                            </div>
                            <CheckMessage name="ValueSize" checkList={checkList} minMaxType={MaxValue} />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-2">
                        <div className="col-3 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text"><b>Opposition Range</b><small className="mx-1">[0.0, 1.0]</small></span>
                            </div>
                        </div>
                        <div className="col-4 mx-3">
                            <div className="d-flex">
                                <div className="input-group-prepend w-100">
                                <span className="input-group-text">Min</span>
                                </div>
                                <input type="text" className={getCheckStyle(checkList, "OppositionRange", MinValue)} placeholder="0.0"
                                   value={oppositionRange[0]}
                                   onInput={(e) => checkForMinMaxType(e, oppositionRange, setOppositionRange, checkList, setCheckList, "OppositionRange", MinValue)}
                                   disabled={disabledAll}/>
                            </div>
                            <CheckMessage name="OppositionRange" checkList={checkList} minMaxType={MinValue} />
                        </div>
                        <div className="col-4 mx-1">
                            <div className="d-flex">
                                <div className="input-group-prepend w-100">
                                <span className="input-group-text">Max</span>
                            </div>
                                <input type="text" className={getCheckStyle(checkList, "OppositionRange", MaxValue)} placeholder="1.0"
                                   value={oppositionRange[1]}
                                   onInput={(e) => checkForMinMaxType(e, oppositionRange, setOppositionRange, checkList, setCheckList, "OppositionRange", MaxValue)}
                                   disabled={disabledAll}/>
                            </div>
                            <CheckMessage name="OppositionRange" checkList={checkList} minMaxType={MaxValue} />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-2">
                        <div className="col-3 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text"><b>Balance Score Range</b><small className="mx-1">[0.0, 1.0]</small></span>
                            </div>
                        </div>
                        <div className="col-4 mx-3">
                            <div className="d-flex">
                                <div className="input-group-prepend w-100">
                                <span className="input-group-text">Min</span>
                            </div>
                                <input type="text" className={getCheckStyle(checkList, "BalanceScoreRange", MinValue)} placeholder="0.0"
                                   value={balanceScoreRange[0]}
                                   onInput={(e) => checkForMinMaxType(e, balanceScoreRange, setBalanceScoreRange, checkList, setCheckList, "BalanceScoreRange", MinValue)}
                                   disabled={disabledAll}/>
                            </div>
                            <CheckMessage name="BalanceScoreRange" checkList={checkList} minMaxType={MinValue} />
                        </div>
                        <div className="col-4 mx-1">
                            <div className="d-flex">
                                <div className="input-group-prepend w-100">
                                <span className="input-group-text">Max</span>
                            </div>
                                <input type="text" className={getCheckStyle(checkList, "BalanceScoreRange", MaxValue)} placeholder="0.05"
                                   value={balanceScoreRange[1]}
                                   onInput={(e) => checkForMinMaxType(e, balanceScoreRange, setBalanceScoreRange, checkList, setCheckList, "BalanceScoreRange", MaxValue)}
                                   disabled={disabledAll}/>
                            </div>
                            <CheckMessage name="BalanceScoreRange" checkList={checkList} minMaxType={MaxValue} />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-2">
                        <div className="col-3 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text"><b>Reservation Value for Profile A</b><small className="mx-1">[0.0, 1.0]</small></span>
                            </div>
                        </div>
                        <div className="col-4 mx-3">
                            <input type="text" className={getCheckStyle(checkList, "ReservationValueA")} placeholder="0.0"
                                   value={reservationValueA}
                                   onInput={(e) => checkForType(e, setReservationValueA, checkList, setCheckList, "ReservationValueA", FloatType)}
                                   disabled={disabledAll}/>
                            <CheckMessage name="ReservationValueA" checkList={checkList} />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-2">
                        <div className="col-3 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text"><b>Reservation Value for Profile B</b><small className="mx-1">[0.0, 1.0]</small></span>
                            </div>
                        </div>
                        <div className="col-4 mx-3">
                            <input type="text" className={getCheckStyle(checkList, "ReservationValueB")} placeholder="0.0"
                                   value={reservationValueB}
                                   onInput={(e) => checkForType(e, setReservationValueB, checkList, setCheckList, "ReservationValueB", FloatType)}
                                   disabled={disabledAll}/>
                            <CheckMessage name="ReservationValueB" checkList={checkList} />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-2">
                        <div className="col-3 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text"><b>Min. Utility</b><small className="mx-1">[0.0, 1.0]</small></span>
                            </div>
                        </div>
                        <div className="col-4 mx-3">
                            <input type="text" className={getCheckStyle(checkList, "UtilityRange", MinValue)} placeholder="0.0"
                                   value={utilityRange[0]}
                                   onInput={(e) => checkForMinMaxType(e, utilityRange, setUtilityRange, checkList, setCheckList, "UtilityRange", MinValue)}
                                   disabled={disabledAll}/>
                            <CheckMessage name="UtilityRange" checkList={checkList} minMaxType={MinValue} />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-2">
                        <div className="col-3 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text"><b>Max. Utility</b><small className="mx-1">[0.0, 1.0]</small></span>
                            </div>
                        </div>
                        <div className="col-4 mx-3">
                            <input type="text" className={getCheckStyle(checkList, "UtilityRange", MaxValue)} placeholder="1.0"
                                   value={utilityRange[1]}
                                   onInput={(e) => checkForMinMaxType(e, utilityRange, setUtilityRange, checkList, setCheckList, "UtilityRange", MaxValue)}
                                   disabled={disabledAll}/>
                            <CheckMessage name="UtilityRange" checkList={checkList} minMaxType={MaxValue} />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-2">
                        <div className="col-3 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text"><b>Utility Distribution</b></span>
                            </div>
                        </div>
                        <div className="col-4 mx-3">
                            <select className="form-select" value={hasRandomness} disabled={disabledAll}
                                    onChange={(e) => setHasRandomness(e.target.value)}>
                                <option value={true}>Random</option>
                                <option value={false}>Uniform</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row my-2 mx-1 bg-secondary p-1">
                <div className="d-flex justify-content-between">
                    <button className="btn btn-light rounded" disabled={disabledAll} onClick={goBack}>Home Page</button>
                    {disabledAll && <h3><b>Please, wait...</b></h3>}
                    <button className="btn btn-primary rounded" onClick={handleSubmit}
                            disabled={disabledSubmit}>{editDomain === null ? "Create" : "Edit"}
                    </button>
                </div>
            </div>
            {errorModal && <InfoModal modalType={MODAL_ERROR} setModal={setErrorModal} message={errorMessage}/>}
            {successModal && <InfoModal modalType={MODAL_SUCCESS} setModal={setSuccessModal} message={successMessage}
                                        closeHandler={closeSuccessModal} />}
        </div>
    );
};

export default DomainGeneratorPage;
