import * as types from "./types"


export const setConfig = (settings) => {
    return {
        type: types.SET_CONFIG,
        payload: {...settings}
    };
};


export const updateSettings = (settings) => {
    return {
        type: types.UPDATE_SETTINGS,
        payload: {...settings}
    };
};

export const updateAgents = (agents) => {
    return {
        type: types.UPDATE_AGENTS,
        payload: [...agents]
    };
};

export const updateOpponentModels = (opponentModels) => {
    return {
        type: types.UPDATE_OPPONENT_MODELS,
        payload: [...opponentModels]
    };
};

export const updateLoggers = (loggers) => {
    return {
        type: types.UPDATE_LOGGERS,
        payload: [...loggers]
    };
};

export const updateDomains = (domains) => {
    return {
        type: types.UPDATE_DOMAINS,
        payload: [...domains]
    };
};
