import {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {DragDropContext, Draggable, Droppable} from "react-beautiful-dnd";
import {updateAgents} from "../../../actions/config";
import {doubleClickedMove, getSelectionStyle, move, reorder} from "./utils";
import axios from "axios";
import InfoModal, {MODAL_ERROR} from "../../modals/InfoModal";
import {SERVER_ADDRESS, CLIENT_ADDRESS} from "../../../config";


const fetchAgents = async () => {
    try {
        const response = await axios.get(SERVER_ADDRESS + "/fetch/agents",
            {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};

const checkAgent = async (path) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/check/agent_path",
            JSON.stringify({path: path}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};

const AgentsPage = ({setPage}) => {
    const [agentMap, setAgentMap] = useState(null);
    const [remainingAgents, setRemainingAgents] = useState([]);
    const [agents, setAgents] = useState([]);

    const [selectedAgents, setSelectedAgents] = useState([]);
    const [selectedPool, setSelectedPool] = useState([]);

    const [agentPath, setAgentPath] = useState("");

    const [disabledAdd, setDisabledAdd] = useState(true);
    const [disabledSubmit, setDisabledSubmit] = useState(true);
    const [disabledAddAll, setDisabledAddAll] = useState(false);
    const [disabledRemoveAll, setDisabledRemoveAll] = useState(true);

    const [errorModal, setErrorModal] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    const configAgents = useSelector(state => state.config.agents);

    const dispatch = useDispatch();

    const handleAgentPath = (e) => {
        setAgentPath(e.target.value);

        if (e.target.value.length === 0) {
            setDisabledAdd(true);
        } else {
            setDisabledAdd(false);
        }
    };

    const handleAdd = async () => {
        setDisabledAdd(true);

        let checkForDuplicate = false;

        for (const [key, value] of Object.entries(agentMap)) {
            if (value === agentPath) {
                checkForDuplicate = true;
                break;
            }
        }

        if (checkForDuplicate) {
            setErrorMessage("Agent already exists!");
            setErrorModal(true);

            setDisabledAdd(false);
        }
        else if (!disabledAdd && agentPath !== '' && !agents.includes(agentPath) && !remainingAgents.includes(agentPath)) {
            const response = await checkAgent(agentPath);

            if (response.error) {
                setErrorMessage(response.errorMessage);
                setErrorModal(true);

                setDisabledAdd(agentPath === '');
            } else {
                let newAgents = [...agents];

                newAgents.push(response["class_name"]);

                let newAgentMap = {...agentMap};

                newAgentMap[response["class_name"]] = response["path"];

                setAgentMap(newAgentMap);
                setAgents(newAgents);

                setDisabledAdd(true);
                setAgentPath("");
            }
        }
    };

    const handleKeyUp = (e) => {
        if (e.key === 'Enter')
            handleAdd();
    };

    const handleSubmit = () => {
        if (agents.length < 2) {
            setErrorMessage("You must select at least two agents.");
            setErrorModal(true);

            return;
        }

        setDisabledAddAll(true);
        setDisabledRemoveAll(true);
        setDisabledAdd(true);
        setDisabledSubmit(true);

        let paths = [];

        for (let i in agents) {
            paths.push(agentMap[agents[i]]);
        }

        dispatch(updateAgents(paths));

        setPage("DOMAINS");
    };

    const handleAddAll = () => {
        let newAgents = [...agents];
        let newRemainingAgents = [];

        for (let i in remainingAgents) {
            if (selectedPool.length === 0 || selectedPool.includes(parseInt(i))) {
                newAgents.push(remainingAgents[i]);
            } else if (selectedPool.length > 0 && !selectedPool.includes(parseInt(i))) {
                newRemainingAgents.push(remainingAgents[i]);
            }
        }

        setRemainingAgents(newRemainingAgents);
        setSelectedPool([]);
        setAgents(newAgents);
    };

    const handleRemoveAll = () => {
        let newRemainingAgents = [...remainingAgents];
        let newAgents = [];

        for (let i in agents) {
            if (selectedAgents.length === 0 || selectedAgents.includes(parseInt(i))) {
                newRemainingAgents.push(agents[i]);
            } else if (selectedAgents.length > 0 && !selectedAgents.includes(parseInt(i))) {
                newAgents.push(agents[i]);
            }
        }

        setSelectedAgents([]);
        setRemainingAgents(newRemainingAgents);
        setAgents(newAgents);
    };

    const onDragEnd = (result) => {
        const {source, destination} = result;

        if (!destination) {
            return;
        }

        if (source.droppableId === destination.droppableId) {
            const items = reorder(
                source.droppableId === "Selected" ? agents : remainingAgents,
                source.index,
                destination.index
            );

            if (source.droppableId === 'Selected') {
                setAgents(items);
                setSelectedAgents([]);
            } else {
                setRemainingAgents(items);
                setSelectedPool([]);
            }
        } else {
            const result = move(
                source.droppableId === "Selected" ? agents : remainingAgents,
                destination.droppableId === "Selected" ? agents : remainingAgents,
                source,
                destination
            );

            setAgents(result["Selected"]);
            setRemainingAgents(result["Others"]);

            if (source.droppableId === 'Selected')
                setSelectedAgents([]);
            else
                setSelectedPool([]);
        }
    };

    const onClickToSelect = (index, selectionList, setSelectionList) => {
        if (selectionList.includes(index)) {
            let newSelectedDomains = [...selectionList];

            const i = newSelectedDomains.indexOf(index);

            newSelectedDomains.splice(i, 1);

            setSelectionList(newSelectedDomains);
        } else {
            let newSelectedDomains = [...selectionList];

            newSelectedDomains.push(index);

            setSelectionList(newSelectedDomains);
        }
    };

    const onDoubleClick = (index, sourceList, destinationList, setSourceList, setDestinationList) => {
        const result = doubleClickedMove(index, sourceList, destinationList);

        setSourceList(result["Source"]);
        setDestinationList(result["Target"]);

        setSelectedAgents([]);
        setSelectedPool([]);
    };

    useEffect(() => {
        setDisabledSubmit(agentMap === null);
    }, [agentMap]);

    useEffect(() => {
        if (agentMap && agents.length === 0 && remainingAgents.length === 0) {
            let newAgents = [];

            for (let i in configAgents) {
                for (const [key, value] of Object.entries(agentMap)) {
                    if (configAgents[i] === value) {
                        newAgents.push(key);
                        break;
                    }
                }
            }

            setAgents(newAgents);

            let newPool = [];

            for (const [key, value] of Object.entries(agentMap)) {
                if (!configAgents.includes(value)) {
                    newPool.push(key);
                }
            }

            setRemainingAgents(newPool);
        }


    }, [configAgents, agentMap]);

    useEffect(() => {
        if (!agentMap) {
            fetchAgents().then((response) => {
                if (response.error) {
                    setErrorMessage(response.errorMessage);
                    setErrorModal(true);
                } else {
                    const agent_list = response["agents"];

                    setAgentMap(agent_list);
                }
            });
        }
    }, [agentMap]);

    useEffect(() => {
        setDisabledAddAll(selectedPool.length === 0 && remainingAgents.length === 0);
    }, [selectedPool, remainingAgents]);

    useEffect(() => {
        setDisabledRemoveAll(selectedAgents.length === 0 && agents.length === 0);
    }, [selectedAgents, agents]);

    useEffect(() => {
        setDisabledAdd(agentPath === '');
    }, [agentPath]);

    return (
        <div className="col">
            <div className="row" style={{height: "15vh"}}>
                <div className="d-flex justify-content-center align-items-center">
                    <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <span className="input-group-text" id="label_agent_path">Agent Path</span>
                        </div>
                        <input type="text" className="form-control" placeholder="path.to.agent.AgentClass"
                               aria-label="AgentPath" aria-describedby="label_agent_path" value={agentPath}
                               onInput={handleAgentPath} onKeyUp={handleKeyUp}/>
                        <button className="btn btn-outline-primary rounded mx-1" onClick={handleAdd} disabled={disabledAdd}>Add
                        </button>
                    </div>
                </div>
            </div>
            <div className="row overflow-hidden" style={{height: "75vh"}}>
                <div className="d-flex justify-content-between">
                    <DragDropContext onDragEnd={onDragEnd}>
                        <Droppable droppableId={"Others"} key={"Others"}>
                            {(provided, snapshot) => (
                                <div
                                    className="col-5 rounded border border-2 bg-white overflow-scroll shadow-sm" {...provided.droppableProps}
                                    ref={provided.innerRef} style={{height: "70vh"}}>
                                    <div className="row m-1 p-1 mb-2 border-bottom border-1">
                                            <div className="d-inline-flex justify-content-center">
                                                <b>Agent Pool</b>
                                            </div>
                                    </div>
                                    {agentMap === null && <h3 className="m-1"><b>Loading...</b></h3>}
                                    {agentMap !== null && remainingAgents.length === 0 && <span className="m-1">No remaining agent.</span>}
                                    {remainingAgents.length > 0 && remainingAgents.map((agentName, index) => (
                                        <Draggable key={agentName} draggableId={agentName} index={index}>
                                            {(provided, snapshot) => (
                                                <div className={getSelectionStyle(index, selectedPool)}
                                                     ref={provided.innerRef}
                                                     onClick={() => onClickToSelect(index, selectedPool, setSelectedPool)}
                                                     onDoubleClick={() => onDoubleClick(index, remainingAgents, agents, setRemainingAgents, setAgents)}
                                                     {...provided.draggableProps}
                                                     {...provided.dragHandleProps}
                                                     style={{...provided.draggableProps.style}}>
                                                    <span>{agentName}</span>
                                                </div>
                                            )}
                                        </Draggable>
                                    ))}
                                    {provided.placeholder}
                                </div>
                            )}
                        </Droppable>
                        <div className="col-1" style={{height: "70vh"}}>
                            <div className="d-flex justify-content-between align-items-center" style={{height: "70vh"}}>
                                <button className="btn btn-outline-secondary rounded" onClick={handleAddAll} disabled={disabledAddAll}>{">>"}</button>
                                <button className="btn btn-outline-secondary rounded" onClick={handleRemoveAll} disabled={disabledRemoveAll}>{"<<"}</button>
                            </div>
                        </div>
                        <Droppable droppableId={"Selected"} key={"Selected"}>
                            {(provided, snapshot) => (
                                <div
                                    className="col-5 rounded border border-2 bg-white overflow-scroll shadow-sm" {...provided.droppableProps}
                                    ref={provided.innerRef} style={{height: "70vh"}}>
                                      <div className="row m-1 p-1 mb-2 border-bottom border-1">
                                            <div className="d-inline-flex justify-content-center">
                                                <b>Selected Agents</b>
                                            </div>
                                        </div>
                                    {agentMap === null && <h3 className="m-1"><b>Loading...</b></h3>}
                                    {agentMap !== null && agents.length === 0 && <span className="m-1">No agent has been selected yet!</span>}
                                    {agents.length > 0 && agents.map((agentName, index) => (
                                        <Draggable key={agentName} draggableId={agentName} index={index}>
                                            {(provided, snapshot) => (
                                                <div className={getSelectionStyle(index, selectedAgents)}
                                                     ref={provided.innerRef}
                                                     onClick={() => onClickToSelect(index, selectedAgents, setSelectedAgents)}
                                                     onDoubleClick={() => onDoubleClick(index, agents, remainingAgents, setAgents, setRemainingAgents)}
                                                     {...provided.draggableProps}
                                                     {...provided.dragHandleProps}
                                                     style={{...provided.draggableProps.style}}>
                                                    <span>{agentName}</span>
                                                </div>
                                            )}
                                        </Draggable>
                                    ))}
                                    {provided.placeholder}
                                </div>
                            )}
                        </Droppable>
                    </DragDropContext>
                </div>
            </div>
            <div className="row my-2 mx-1 bg-secondary p-1 w-100">
                <div className="d-flex justify-content-between">
                    <button className="btn btn-light rounded" onClick={() => setPage("MAIN")}>Home Page</button>
                    <button className="btn btn-primary rounded" onClick={handleSubmit} disabled={disabledSubmit}>Next</button>
                </div>
            </div>
            {errorModal && <InfoModal modalType={MODAL_ERROR} setModal={setErrorModal} message={errorMessage}/>}
        </div>
    );
};

export default AgentsPage;
