import React, {useEffect, useState} from "react";
import { Modal } from "react-bootstrap";


const EditValueModal = ({profile, setProfile, otherProfile, setOtherProfile, setModal, issueName, value}) => {
    const [valueName, setValueName] = useState(value);
    const [utility, setUtility] = useState(profile["issues"][issueName][value]);

    const [errorMessageUtility, setErrorMessageUtility] = useState("");
    const [errorMessageName, setErrorMessageName] = useState("");

    const [disabledSubmit, setDisabledSubmit] = useState(false);
    const [disabledAll, setDisabledAll] = useState(false);

    const openHandler = () => {
        setModal(true);
    };

    const closeHandler = () => {
        setModal(false);
    };

    const handleChangeValue = (e) => {
        if (!disabledAll) {
            setUtility(e.target.value);

            try {
                const val = parseFloat(e.target.value);

                if (isNaN(val)) {
                    setErrorMessageUtility("Enter a valid value.");
                    return;
                }

                if (val < 0 || val > 1) {
                    setErrorMessageUtility("Value utility must be in range [0.0, 1.0]");
                    return;
                }

                setErrorMessageUtility("");

            } catch (e) {
                setErrorMessageUtility("Enter a valid value.");
            }
        }
    };

    const handleChangeName = (e) => {
        if (!disabledAll) {
            setValueName(e.target.value);

            if (!e.target.value || e.target.value === '') {
                setErrorMessageName("Enter a valid name.");

                return;
            }

            if (profile["issues"][issueName].hasOwnProperty(e.target.value) && e.target.value !== value) {
                setErrorMessageName("Value names must be identical.");

                return;
            }

            setErrorMessageName("");
        }
    };

    useEffect(() => {
        setDisabledSubmit(disabledAll || errorMessageName !== '' || errorMessageUtility !== '');
    }, [disabledAll, errorMessageName, errorMessageUtility]);

    const onSubmit = async () => {
        if (!disabledAll && !disabledSubmit) {
            setDisabledAll(true);

            let newProfile = {...profile};

            newProfile["issues"][issueName][valueName] = parseFloat(utility);

            if (valueName !== value) {
                delete newProfile["issues"][issueName][value];

                let newOtherProfile = {...otherProfile};

                newOtherProfile["issues"][issueName][valueName] = newOtherProfile["issues"][issueName][value];

                delete newOtherProfile["issues"][issueName][value];

                setOtherProfile(newOtherProfile);
            }

            setProfile(newProfile);

            setDisabledAll(false);

            setModal(false);
        }
    };

    return (
        <Modal show={openHandler} onHide={closeHandler} onEscapeKeyDown={closeHandler}>
            <Modal.Header className="bg-secondary" closeButton>
                <Modal.Title>Edit Value</Modal.Title>
            </Modal.Header>
            <Modal.Body className="bg-light">
                <div className="row m-2 p-2 w-100">
                    <div className="input-group w-100">
                        <div className="col-6 d-flex align-items-center">
                            <div className="input-group-prepend w-100">
                                <b>Issue Name</b>
                            </div>
                        </div>
                        <div className="col-5 mx-2">
                            <input
                                className="form-control"
                                type="text" value={issueName}
                                disabled={true}
                            />
                        </div>
                    </div>
                </div>
                <div className="row m-2 p-2 w-100">
                    <div className="input-group w-100">
                        <div className="col-6 d-flex align-items-center">
                            <div className="input-group-prepend w-100">
                                <b>Value Name</b>
                            </div>
                        </div>
                        <div className="col-5 mx-2">
                            <input
                                className={errorMessageName !== '' ? "form-control border-danger border-2" : "form-control"}
                                type="text" value={valueName}
                                onInput={(e) => handleChangeName(e)}
                                disabled={disabledAll}
                            />
                            {errorMessageName !== '' && <small className="text-danger px-2">{errorMessageName}</small>}
                        </div>
                    </div>
                </div>
                <div className="row m-2 p-2 w-100">
                    <div className="input-group w-100">
                        <div className="col-6 d-flex align-items-center">
                            <div className="input-group-prepend w-100">
                                <b>Value Utility</b><small className="mx-1">[0.0, 1.0]</small>
                            </div>
                        </div>
                        <div className="col-5 mx-2">
                            <input
                                className={errorMessageUtility !== '' ? "form-control border-danger border-2" : "form-control"}
                                type="text" value={utility}
                                onInput={(e) => handleChangeValue(e)}
                                disabled={disabledAll}
                            />
                            {errorMessageUtility !== '' && <small className="text-danger px-2">{errorMessageUtility}</small>}
                        </div>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <div className="row  w-100">
                    <div className="d-flex justify-content-between w-100">
                        <button className="btn btn-secondary rounded" onClick={closeHandler} disabled={disabledAll}>Cancel</button>
                        <button className="btn btn-success rounded" onClick={onSubmit} disabled={disabledAll || disabledSubmit}>Save</button>
                    </div>
                </div>
            </Modal.Footer>
        </Modal>
    );
};

export default EditValueModal;
