import React, {useEffect, useState} from "react";
import { Modal } from "react-bootstrap";


const EditIssueModal = ({profile, setProfile, otherProfile, setOtherProfile, setModal, issue}) => {
    const [issueName, setIssueName] = useState(issue);
    const [weight, setWeight] = useState(profile["issueWeights"][issue]);

    const [errorMessageWeight, setErrorMessageWeight] = useState("");
    const [errorMessageName, setErrorMessageName] = useState("");

    const [disabledSubmit, setDisabledSubmit] = useState(false);
    const [disabledAll, setDisabledAll] = useState(false);

    const openHandler = () => {
        setModal(true);
    };

    const closeHandler = () => {
        setModal(false);
    };

    const handleChangeValue = (e) => {
        if (!disabledAll) {
            setWeight(e.target.value);

            try {
                const val = parseFloat(e.target.value);

                if (isNaN(val)) {
                    setErrorMessageWeight("Enter a valid value.");
                    return;
                }

                if (val < 0 || val > 1) {
                    setErrorMessageWeight("Issue weight must be in range [0.0, 1.0]");
                    return;
                }

                setErrorMessageWeight("");

            } catch (e) {
                setErrorMessageWeight("Enter a valid value.");
            }
        }
    };

    const handleChangeName = (e) => {
        if (!disabledAll) {
            setIssueName(e.target.value);

            if (!e.target.value || e.target.value === '') {
                setErrorMessageName("Enter a valid name.");

                return;
            }

            if (profile["issueWeights"].hasOwnProperty(e.target.value) && e.target.value !== issue) {
                setErrorMessageName("Issue names must be identical.");

                return;
            }

            setErrorMessageName("");
        }
    };

    useEffect(() => {
        setDisabledSubmit(disabledAll || errorMessageName !== '' || errorMessageWeight !== '');
    }, [disabledAll, errorMessageName, errorMessageWeight]);

    const onSubmit = async () => {
        if (!disabledAll && !disabledSubmit) {
            setDisabledAll(true);

            let newProfile = {...profile};

            newProfile["issueWeights"][issueName] = parseFloat(weight);

            if (issueName !== issue) {
                delete newProfile["issueWeights"][issue];

                let newOtherProfile = {...otherProfile};

                newOtherProfile["issueWeights"][issueName] = newOtherProfile["issueWeights"][issue];

                delete newOtherProfile["issueWeights"][issue];

                setOtherProfile(newOtherProfile);
            }

            setProfile(newProfile);

            setDisabledAll(false);

            setModal(false);
        }
    };

    return (
        <Modal show={openHandler} onHide={closeHandler} onEscapeKeyDown={closeHandler}>
            <Modal.Header className="bg-secondary" closeButton>
                <Modal.Title>Edit Issue</Modal.Title>
            </Modal.Header>
            <Modal.Body className="bg-light">
                <div className="row m-2 p-2 w-100">
                    <div className="input-group w-100">
                        <div className="col-6 d-flex align-items-center">
                            <div className="input-group-prepend w-100">
                                <b>Issue Name</b>
                            </div>
                        </div>
                        <div className="col-5 mx-2">
                            <input
                                className={errorMessageName !== '' ? "form-control border-danger border-2" : "form-control"}
                                type="text" value={issueName}
                                onInput={(e) => handleChangeName(e)}
                                disabled={disabledAll}
                            />
                            {errorMessageName !== '' && <small className="text-danger px-2">{errorMessageName}</small>}
                        </div>
                    </div>
                </div>
                <div className="row m-2 p-2 w-100">
                    <div className="input-group w-100">
                        <div className="col-6 d-flex align-items-center">
                            <div className="input-group-prepend w-100">
                                <b>Issue Weight</b><small className="mx-1">[0.0, 1.0]</small>
                            </div>
                        </div>
                        <div className="col-5 mx-2">
                            <input
                                className={errorMessageWeight !== '' ? "form-control border-danger border-2" : "form-control"}
                                type="text" value={weight}
                                onInput={(e) => handleChangeValue(e)}
                                disabled={disabledAll}
                            />
                            {errorMessageWeight !== '' && <small className="text-danger px-2">{errorMessageWeight}</small>}
                        </div>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <div className="row  w-100">
                    <div className="d-flex justify-content-between w-100">
                        <button className="btn btn-secondary rounded" onClick={closeHandler} disabled={disabledAll}>Cancel</button>
                        <button className="btn btn-success rounded" onClick={onSubmit} disabled={disabledAll || disabledSubmit}>Save</button>
                    </div>
                </div>
            </Modal.Footer>
        </Modal>
    );
};

export default EditIssueModal;
