import React, {useState} from "react";
import { Modal } from "react-bootstrap";


const EditReservationValueModal = ({profile, setProfile, setModal}) => {
    const [reservationValue, setReservationValue] = useState(profile["reservationValue"]);

    const [errorMessage, setErrorMessage] = useState("");

    const [disabledSubmit, setDisabledSubmit] = useState(false);
    const [disabledAll, setDisabledAll] = useState(false);

    const openHandler = () => {
        setModal(true);
    };

    const closeHandler = () => {
        setModal(false);
    };

    const handleChange = (e) => {
        if (!disabledAll) {
            setReservationValue(e.target.value);

            try {
                const val = parseFloat(e.target.value);

                if (isNaN(val)) {
                    setErrorMessage("Enter a valid value.");
                    setDisabledSubmit(true);
                    return;
                }

                if (val < 0 || val > 1) {
                    setErrorMessage("Reservation value must be in range [0.0, 1.0]");
                    setDisabledSubmit(true);
                    return;
                }

                setErrorMessage("");
                setDisabledSubmit(false);

            } catch (e) {
                setDisabledSubmit(true);
                setErrorMessage("Enter a valid value.");
            }
        }
    };

    const onSubmit = async () => {
        if (!disabledAll && !disabledSubmit) {
            setDisabledAll(true);

            setProfile({...profile, "reservationValue": parseFloat(reservationValue)});

            setDisabledAll(false);

            setModal(false);
        }
    };

    return (
        <Modal show={openHandler} onHide={closeHandler} onEscapeKeyDown={closeHandler}>
            <Modal.Header className="bg-secondary" closeButton>
                <Modal.Title>Edit Reservation Value</Modal.Title>
            </Modal.Header>
            <Modal.Body className="bg-light">
                <div className="row m-2 p-2 w-100">
                    <div className="input-group w-100">
                        <div className="col-6 d-flex align-items-center">
                            <div className="input-group-prepend w-100">
                                <b>Reservation Value</b><small className="mx-1">[0.0, 1.0]</small>
                            </div>
                        </div>
                        <div className="col-5 mx-2">
                            <input
                                className={errorMessage !== '' ? "form-control border-danger border-2" : "form-control"}
                                type="text" value={reservationValue}
                                onInput={(e) => handleChange(e)}
                                disabled={disabledAll}
                            />
                            {errorMessage !== '' && <small className="text-danger px-2">{errorMessage}</small>}
                        </div>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <div className="row  w-100">
                    <div className="d-flex justify-content-between w-100">
                        <button className="btn btn-secondary rounded" onClick={closeHandler} disabled={disabledAll}>Cancel</button>
                        <button className="btn btn-success rounded" onClick={onSubmit} disabled={disabledAll || disabledSubmit}>Save</button>
                    </div>
                </div>
            </Modal.Footer>
        </Modal>
    );
};

export default EditReservationValueModal;
