import {useEffect, useState} from "react";
import InfoModal, {MODAL_ERROR} from "../modals/InfoModal";
import checkForType, {CheckMessage, getCheckStyle, IntegerType, StringType} from "./DomainGeneratorCheck";
import axios from "axios";
import {useDispatch} from "react-redux";
import {setDomain} from "../../actions/domainGenerator";
import {SERVER_ADDRESS, CLIENT_ADDRESS} from "../../config";


const submit = async (numberOfIssues, numberOfValuesPerIssue, name) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/create/domain",
            JSON.stringify({
                "name": name,
                "numberOfIssues": numberOfIssues,
                "numberOfValuesPerIssue": numberOfValuesPerIssue
            }), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const ManualGeneratorPage = ({setPage}) => {
    const [numberOfIssues, setNumberOfIssues] = useState(0);

    const [numberOfValuesPerIssue, setNumberOfValuesPerIssue] = useState({});

    const [name, setName] = useState("");

    const [disabledAll, setDisabledAll] = useState(false);
    const [disabledSubmit, setDisabledSubmit] = useState(true);

    const [errorModal, setErrorModal] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    const [checkList, setCheckList] = useState({});

    const dispatch = useDispatch();

    const setWrapper = (issueName) => {
        return (value) => {
            setNumberOfValuesPerIssue({...numberOfValuesPerIssue, [issueName]: value});
        };
    };

    const clickToSubmit = async () => {
        if (!disabledAll && !disabledSubmit) {
            setDisabledAll(true);

            const response = await submit(numberOfIssues, numberOfValuesPerIssue, name);

            if (response.error) {
                setErrorMessage(response.errorMessage);
                setErrorModal(true);
            } else {
                dispatch(setDomain(response["result"]["DomainName"]));

                setPage("EDIT_DOMAIN");
            }

            setDisabledAll(false);
        }
    };

    useEffect(() => {
        if (disabledAll) {
            return;
        }

        let newNumberOfValuesPerIssue = {};

        for (let i = 0; i < numberOfIssues; ++i) {
            if (numberOfValuesPerIssue.hasOwnProperty(i)) {
                newNumberOfValuesPerIssue[i] = numberOfValuesPerIssue[i];
            } else {
                newNumberOfValuesPerIssue[i] = 0
            }
        }

        if (newNumberOfValuesPerIssue.toString() !== numberOfValuesPerIssue.toString())
            setNumberOfValuesPerIssue(newNumberOfValuesPerIssue);

        if (numberOfIssues <= 0) {
            if (numberOfValuesPerIssue.toString() !== {}.toString())
                setNumberOfValuesPerIssue({});
            setDisabledSubmit(true);
        } else if (checkList.hasOwnProperty("NumberOfIssues") && checkList["NumberOfIssues"] !== '') {
            setDisabledSubmit(true);
        } else if (name === '' || (checkList.hasOwnProperty("Name") && checkList["Name"] !== '')) {
            setDisabledSubmit(true);
        } else {
            for (const [key, value] of Object.entries(newNumberOfValuesPerIssue)) {
                if (checkList.hasOwnProperty("NumberOfValuesInIssue" + (key + 1)) && checkList["NumberOfValuesInIssue" + (key + 1)] !== '') {
                    setDisabledSubmit(true);
                    return;
                }
            }
            setDisabledSubmit(false);
        }

    }, [numberOfIssues, name, numberOfValuesPerIssue, checkList]);

    return (
        <>
            <div className="row py-2 border border-1 rounded overflow-auto" style={{height: "90vh"}}>
                <div className="flex-column justify-content-start">
                    <div className="row p-1">
                        <div className="input-group my-2">
                            <div className="col-3 d-flex">
                                <div className="input-group-prepend w-100">
                                    <span className="input-group-text"><b>Name</b></span>
                                </div>
                            </div>
                            <div className="col-6 mx-3">
                                <input type="text" className={getCheckStyle(checkList, "Name")} placeholder="Example"
                                       onInput={(e) => checkForType(e, setName, checkList, setCheckList, "Name", StringType)}
                                       value={name} disabled={disabledAll}/>

                                <CheckMessage name="Name" checkList={checkList} />
                            </div>
                        </div>
                    </div>
                    <div className="row p-1">
                        <div className="input-group my-2">
                            <div className="col-3 d-flex">
                                <div className="input-group-prepend w-100">
                                    <span className="input-group-text"><b>Number of Issues</b></span>
                                </div>
                            </div>
                            <div className="col-6 mx-3">
                                <input type="text" className={getCheckStyle(checkList, "NumberOfIssues")} placeholder=""
                                       onInput={(e) => checkForType(e, setNumberOfIssues, checkList, setCheckList, "NumberOfIssues", IntegerType)}
                                       value={numberOfIssues} disabled={disabledAll}/>

                                <CheckMessage name="NumberOfIssues" checkList={checkList} />
                            </div>
                        </div>
                    </div>
                    {numberOfIssues > 0 && Array.from(Array(parseInt(numberOfIssues)), (key, index) => (
                        <div className="row p-1" key={"Issue" + (index + 1)}>
                            <div className="input-group my-2">
                                <div className="col-3 d-flex">
                                    <div className="input-group-prepend w-100">
                                        <span className="input-group-text"><b>Number of Values in Issue {index + 1}</b></span>
                                    </div>
                                </div>
                                <div className="col-6 mx-3">
                                    <input type="text" className={getCheckStyle(checkList, "NumberOfValuesInIssue" + (index + 1))} placeholder=""
                                           onInput={(e) => checkForType(e, setWrapper(index), checkList, setCheckList, "NumberOfValuesInIssue" + (index + 1), IntegerType)}
                                           value={numberOfValuesPerIssue.hasOwnProperty(index) ? numberOfValuesPerIssue[index] : 0} disabled={disabledAll}/>

                                    <CheckMessage name={"NumberOfValuesInIssue" + (index + 1)} checkList={checkList} />
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
            <div className="row my-2 mx-1 bg-secondary p-1">
                <div className="d-flex justify-content-between">
                    <button className="btn btn-light rounded" disabled={disabledAll}
                            onClick={() => setPage("MAIN")}>Home Page
                    </button>
                    {disabledAll && <h3><b>Please, wait...</b></h3>}
                    <button className="btn btn-primary rounded" onClick={clickToSubmit}
                            disabled={disabledAll || disabledSubmit}>Next
                    </button>
                </div>
            </div>
            {errorModal && <InfoModal modalType={MODAL_ERROR} setModal={setErrorModal} message={errorMessage}/>}
        </>
    );
};


export default ManualGeneratorPage;
