import {useEffect, useState} from "react";
import axios from "axios";
import InfoModal, {MODAL_ERROR, MODAL_INFO, MODAL_SUCCESS} from "../modals/InfoModal";
import {getSelectionStyle} from "./create/utils";
import {useDispatch} from "react-redux";
import {setConfig as SetConfig} from "../../actions/config";
import {initialState} from "../../reducers/config";
import {SERVER_ADDRESS, CLIENT_ADDRESS} from "../../config";


const logger_list = {
    "EstimatorMetricLogger": "Estimation Performance Metrics (round by round)",
    "EstimatorOnlyFinalMetricLogger": "Estimation Performance Metrics (only final)",
    "EstimatedParetoLogger": "Pareto Estimation Performance Evaluation",
    "DefaultLoggers": "Default Loggers",
    "GraphLoggers": "Graph Loggers",
    "OppModelLoggers": "Estimation Loggers (acc. opp. model)"
};


const convertPathToName = (path) => {
    let withoutExtension = ("" + path).replace(".yaml", "").replace(".yml", "");

    if (withoutExtension.includes("\\"))
        return withoutExtension.split("\\")[withoutExtension.split("\\").length - 1];
    else if (withoutExtension.includes("/"))
        return withoutExtension.split("/")[withoutExtension.split("/").length - 1];

    return withoutExtension;
};


const fetchSettings = async () => {
    try {
        const response = await axios.get(SERVER_ADDRESS + "/fetch/tournament_configurations",
            {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const removeConfigRequest = async (selectedConfigurationPath) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/remove/tournament_configuration",
            JSON.stringify({name: selectedConfigurationPath}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const startTournament = async (selectedConfiguration) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/start/tournament",
            JSON.stringify({path: selectedConfiguration}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const TournamentSetting = ({config}) => {
    return (
        <div className="overflow-auto" style={{height: "80vh"}}>
            <div className="row m-1 p-1 mb-2 border-bottom border-1">
                <div className="d-inline-flex justify-content-center">
                    <b>{convertPathToName(config["name"])}</b>
                </div>
            </div>
            <div className="row m-1 p-1">
                <div className="d-inline-flex"><span className="text-danger"><b>agents:</b></span>{config["agents"].length === 0 && <span className="text-muted mx-1">[]</span>}</div>
            </div>
            {config["agents"].length > 0 &&
                <div className="row">
                    <ul>
                        {config["agents"].map((agentName, index) => (
                            <li className="px-5 text-muted" key={agentName}><span>{(index + 1) + ". " + (agentName.includes(".") ? agentName.split(".")[agentName.split(".").length - 1] : agentName)}</span></li>
                        ))}
                    </ul>
                </div>
            }
            <div className="row m-1 p-1">
                <div className="d-inline-flex"><span className="text-danger"><b>domains:</b></span>{config["domains"].length === 0 && <span className="text-muted mx-1">[]</span>}</div>
            </div>
            {config["domains"].length > 0 &&
                <div className="row">
                    <ul>
                        {config["domains"].map((domainName, index) => (
                            <li className="px-5 text-muted" key={domainName}><span className="">{(index + 1) + ". Domain" + domainName}</span></li>
                        ))}
                    </ul>
                </div>
            }
            <div className="row m-1 p-1">
                <div className="d-inline-flex"><span className="text-danger"><b>loggers:</b></span>{config["loggers"].length === 0 && <span className="text-muted mx-1">[]</span>}</div>
            </div>
            {config["loggers"].length > 0 &&
                <div className="row">
                    <ul>
                        {config["loggers"].map((loggerName, index) => (
                            <li className="px-5 text-muted" key={loggerName}><span>{(index + 1) + ". " + (loggerName.includes(".") ? loggerName.split(".")[loggerName.split(".").length - 1] : (logger_list.hasOwnProperty(loggerName) ? logger_list[loggerName] : loggerName))}</span></li>
                        ))}
                    </ul>
                </div>
            }
            <div className="row m-1 p-1">
                <div className="d-inline-flex"><span className="text-danger"><b>estimators:</b></span>{config["estimators"].length === 0 && <span className="text-muted mx-1">[]</span>}</div>
            </div>
            {config["estimators"].length > 0 &&
                <div className="row">
                    <ul>
                        {config["estimators"].map((oppModelName, index) => (
                            <li className="px-5 text-muted" key={oppModelName}><span>{(index + 1) + ". " + (oppModelName.includes(".") ? oppModelName.split(".")[oppModelName.split(".").length - 1] : oppModelName)}</span></li>
                        ))}
                    </ul>
                </div>
            }
            <div className="row m-1 p-1">
                <div className="d-inline-flex">
                    <span className="text-danger"><b>deadline_time:</b></span><span className="text-muted mx-1">{"" + config["deadline_time"]}</span>
                </div>
            </div>
            <div className="row m-1 p-1">
                <div className="d-inline-flex">
                    <span className="text-danger"><b>deadline_round:</b></span><span className="text-muted mx-1">{"" + config["deadline_round"]}</span>
                </div>
            </div>
            <div className="row m-1 p-1">
                <div className="d-inline-flex">
                    <span className="text-danger"><b>self_negotiation:</b></span><span className="text-muted mx-1">{"" + config["self_negotiation"]}</span>
                </div>
            </div>
            <div className="row m-1 p-1">
                <div className="d-inline-flex">
                    <span className="text-danger"><b>repeat:</b></span><span className="text-muted mx-1">{"" + config["repeat"]}</span>
                </div>
            </div>
            <div className="row m-1 p-1">
                <div className="d-inline-flex">
                    <span className="text-danger"><b>result_dir:</b></span><span className="text-muted mx-1">{"" + config["result_dir"]}</span>
                </div>
            </div>
            <div className="row m-1 p-1 d-inline-flex">
                <div className="d-inline-flex">
                    <span className="text-danger"><b>shuffle:</b></span><span className="text-muted mx-1">{"" + config["shuffle"]}</span>
                </div>
            </div>
            <div className="row m-1 p-1">
                <div className="d-inline-flex">
                    <span className="text-danger"><b>drawing_format:</b></span><span className="text-muted mx-1">{"" + config["drawing_format"]}</span>
                </div>
            </div>
            <div className="row m-1 p-1">
                <div className="d-inline-flex">
                    <span className="text-danger"><b>seed:</b></span><span className="text-muted mx-1">{"" + config["seed"]}</span>
                </div>
            </div>
        </div>
    );
};


const RunTournamentsPage = ({setPage}) => {
    const [configs, setConfigs] = useState(null);
    const [selectedConfig, setSelectedConfig] = useState(-1);

    const [errorModal, setErrorModal] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    const [successModal, setSuccessModal] = useState(false);
    const [successMessage, setSuccessMessage] = useState("");

    const [removeModal, setRemoveModal] = useState(false);
    const [removeModalMessage, setRemoveModalMessage] = useState("");

    const [disabledAll, setDisabledAll] = useState(false);

    const dispatch = useDispatch();

    const goBack = () => {
        setPage("MAIN");
    };

    const onClickToSelect = (index) => {
        if (disabledAll)
            return;

        if (index === selectedConfig) {
            setSelectedConfig(-1);
        } else {
            setSelectedConfig(index);
        }
    };

    const onClickToAdd = () => {
        if (!disabledAll) {
            setDisabledAll(true);

            dispatch(SetConfig(initialState));

            setPage("AGENTS");
        }
    };

    const onClickToRemove = () => {
        if (!disabledAll && selectedConfig !== -1) {
            setRemoveModal(true);
            setRemoveModalMessage("Are you sure to remove the tournament config?");
        }
    };

    const removeConfig = async () => {
        if (!disabledAll && selectedConfig !== -1) {
            setRemoveModal(false);
            setDisabledAll(true);

            const name = configs[selectedConfig]["name"];

            setSelectedConfig(-1);

            const response = await removeConfigRequest(name);

            if (response.error) {
                setErrorMessage(response.errorMessage);
                setErrorModal(true);
            } else {
                setSuccessMessage("Selected config is successfully removed.");

                setSuccessModal(true);
            }

            setDisabledAll(false);
        }
    };

    const onCloseSuccessModal = () => {
        if (selectedConfig !== -1) {
            setSelectedConfig(-1);
            setPage("MONITOR_TOURNAMENTS");
        } else {
            setConfigs(null);
        }

        setSuccessModal(false);
    };


    const onClickToEdit = () => {
        if (!disabledAll && selectedConfig !== -1) {
            setDisabledAll(true);

            const setting = {...configs[selectedConfig], name: convertPathToName(configs[selectedConfig]["name"])};

            dispatch(SetConfig(setting));

            setPage("AGENTS");
        }
    };

    const onClickToStart = async () => {
        if (!disabledAll && selectedConfig !== -1) {
            setDisabledAll(true);

            const response = await startTournament(configs[selectedConfig]["name"]);

            if (response.error) {
                setErrorMessage(response.errorMessage);
                setErrorModal(true);
            } else {
                setSuccessMessage("Selected config is successfully started.");

                setSuccessModal(true);
            }

            setDisabledAll(false);
        }
    };

    useEffect(() => {
        if (configs === null) {
            fetchSettings().then((response) => {
                if (response.error) {
                    setErrorMessage(response.errorMessage);
                    setErrorModal(true);

                    return;
                }

                let settings = [];

                for (let i in response.settings) {
                    let loggers = [];

                    if (response.settings[i]["loggers"].includes("MoveAnalyzeLogger")) {
                        if (!loggers.includes("DefaultLoggers"))
                            loggers.push("DefaultLoggers");

                        const index = response.settings[i]["loggers"].indexOf("MoveAnalyzeLogger");
                        response.settings[i]["loggers"].splice(index, 1);
                    }

                    if (response.settings[i]["loggers"].includes("BidSpaceLogger")) {
                        if (!loggers.includes("DefaultLoggers"))
                            loggers.push("DefaultLoggers");

                        const index = response.settings[i]["loggers"].indexOf("BidSpaceLogger");
                        response.settings[i]["loggers"].splice(index, 1);
                    }

                    if (response.settings[i]["loggers"].includes("TournamentSummaryLogger")) {
                        if (!loggers.includes("DefaultLoggers"))
                            loggers.push("DefaultLoggers");

                        const index = response.settings[i]["loggers"].indexOf("TournamentSummaryLogger");
                        response.settings[i]["loggers"].splice(index, 1);
                    }

                    if (response.settings[i]["loggers"].includes("FinalGraphsLogger")) {
                        if (!loggers.includes("GraphLoggers"))
                            loggers.push("GraphLoggers");

                        const index = response.settings[i]["loggers"].indexOf("FinalGraphsLogger");
                        response.settings[i]["loggers"].splice(index, 1);
                    }

                    if (response.settings[i]["loggers"].includes("DomainGraphsLogger")) {
                        if (!loggers.includes("GraphLoggers"))
                            loggers.push("GraphLoggers");

                        const index = response.settings[i]["loggers"].indexOf("DomainGraphsLogger");
                        response.settings[i]["loggers"].splice(index, 1);
                    }

                    if (response.settings[i]["loggers"].includes("UtilityDistributionLogger")) {
                        if (!loggers.includes("GraphLoggers"))
                            loggers.push("GraphLoggers");

                        const index = response.settings[i]["loggers"].indexOf("UtilityDistributionLogger");
                        response.settings[i]["loggers"].splice(index, 1);
                    }

                    if (response.settings[i]["loggers"].includes("EstimatedUtilityLogger")) {
                        if (!loggers.includes("OppModelLoggers"))
                            loggers.push("OppModelLoggers");

                        const index = response.settings[i]["loggers"].indexOf("EstimatedUtilityLogger");
                        response.settings[i]["loggers"].splice(index, 1);
                    }

                    if (response.settings[i]["loggers"].includes("EstimatedMoveLogger")) {
                        if (!loggers.includes("OppModelLoggers"))
                            loggers.push("OppModelLoggers");

                        const index = response.settings[i]["loggers"].indexOf("EstimatedMoveLogger");
                        response.settings[i]["loggers"].splice(index, 1);
                    }

                    if (response.settings[i]["loggers"].includes("EstimatedBidSpaceLogger")) {
                        if (!loggers.includes("OppModelLoggers"))
                            loggers.push("OppModelLoggers");

                        const index = response.settings[i]["loggers"].indexOf("EstimatedBidSpaceLogger");
                        response.settings[i]["loggers"].splice(index, 1);
                    }

                    response.settings[i]["loggers"].sort();

                    response.settings[i]["loggers"] = [...loggers, ...response.settings[i]["loggers"]];

                    response.settings[i]["agents"].sort();
                    response.settings[i]["domains"].sort();
                    response.settings[i]["estimators"].sort();

                    settings.push(response.settings[i]);
                }

                setConfigs(settings);
            });
        }
    }, [configs]);

    return (
        <>
            <div className="row overflow-auto" style={{height: "90vh"}}>
                <div className="d-flex justify-content-between">
                    <div className="col-5 border border-2 rounded overflow-hidden mt-3 mx-3 position-relative shadow-sm"
                         style={{height: "85vh"}}>
                        {configs === null && <div className="row m-1 p-1"><h3><b>Loading...</b></h3></div>}
                        {configs !== null && configs.length === 0 &&
                            <div className="row m-1 p-1"><b>No tournament configuration exist.</b></div>}
                        {configs !== null && configs.length > 0 &&
                            <>
                                <div className="overflow-auto" style={{height: "80vh"}}>
                                    <div className="row m-1 p-1 mb-2 border-bottom border-1">
                                        <div className="d-inline-flex justify-content-center">
                                            <b>Tournament Configurations</b>
                                        </div>
                                    </div>
                                    {configs.map((setting, index) => (
                                        <div className={getSelectionStyle(index, [selectedConfig])}
                                             onClick={() => onClickToSelect(index)} key={index}>
                                            {convertPathToName(setting["name"])}
                                        </div>
                                    ))}
                                </div>
                                <div
                                    className="row bg-secondary p-1 m-0 w-100 border border-1 rounded bottom-0 position-absolute">
                                    <div className="d-flex justify-content-between">
                                        <button className="btn btn-primary" disabled={disabledAll} onClick={onClickToAdd}>Add</button>
                                        <button className="btn btn-danger" disabled={disabledAll || selectedConfig === -1} onClick={onClickToRemove}>Remove</button>
                                    </div>
                                </div>
                            </>
                        }
                    </div>
                    <div className="col-5 border border-2 rounded overflow-hidden mt-3 mx-3 position-relative bg-white shadow-sm"
                         style={{height: "85vh"}}>
                        {selectedConfig !== -1 && <TournamentSetting config={configs[selectedConfig]}/>}
                        <div
                            className="row bg-secondary p-1 m-0 w-100 border border-1 rounded bottom-0 position-absolute">
                            <div className="d-flex justify-content-between">
                                <button className="btn btn-primary" disabled={disabledAll || selectedConfig === -1} onClick={onClickToEdit}>Edit</button>
                                <button className="btn btn-success" disabled={disabledAll || selectedConfig === -1} onClick={onClickToStart}>Run</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row my-2 bg-secondary p-1 position-absolute border-0 w-100">
                <div className="d-flex justify-content-start">
                    <button className="btn btn-light" onClick={goBack} disabled={disabledAll}>Home Page
                    </button>
                </div>
            </div>
            {errorModal && <InfoModal modalType={MODAL_ERROR} setModal={setErrorModal} message={errorMessage}/>}
            {successModal && <InfoModal modalType={MODAL_SUCCESS} setModal={setSuccessModal} message={successMessage} closeHandler={onCloseSuccessModal} />}
            {removeModal &&
                <InfoModal modalType={MODAL_INFO} setModal={setRemoveModal} message={removeModalMessage} buttons={[{
                    "className": "btn btn-danger rounded",
                    "disabled": false,
                    "onClick": removeConfig,
                    "name": "Remove"
                }]}/>}
        </>
    );
};


export default RunTournamentsPage;
