import * as types from "../actions/types"


const initialState = {
    domains: [],
    domain: [],
    editDomain: null,
    removeOnCancel: true,
    showDomain: null
};


const domainGeneratorReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.SET_DOMAINS:
            return {
                ...state,
                domains: action.payload
            };
        case types.SET_DOMAIN:
            return {
                ...state,
                domain: action.payload.domain,
                removeOnCancel: action.payload.remove
            };
        case types.EDIT_DOMAIN:
            return {
                ...state,
                editDomain: action.payload
            };
        case types.SHOW_DOMAIN:
            return {
                ...state,
                showDomain: action.payload
            };
        default:
            return {
                ...state
            };
    }
};

export default domainGeneratorReducer;
