import {useEffect, useState} from "react";
import {updateDomains} from "../../../actions/config";
import {useDispatch, useSelector} from "react-redux";
import axios from "axios";
import InfoModal, {MODAL_ERROR} from "../../modals/InfoModal";
import {SERVER_ADDRESS, CLIENT_ADDRESS} from "../../../config";


const fetchDomains = async () => {
    try {
        const response = await axios.get(SERVER_ADDRESS + "/fetch/domains",
            {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


export const DomainRow = ({domain, index, selectedDomains, setSelectedDomains}) => {
    const [className, setClassName] = useState("");

    useEffect(() => {
        if (selectedDomains.includes(index)) {
            setClassName("table-primary");
        } else {
            setClassName("");
        }
    }, [selectedDomains, index]);

    const onClickToSelect = (index) => {
        if (selectedDomains.includes(index)) {
            let newSelectedDomains = [...selectedDomains];

            const i = newSelectedDomains.indexOf(index);

            newSelectedDomains.splice(i, 1);

            setSelectedDomains(newSelectedDomains);
        } else {
            let newSelectedDomains = [...selectedDomains];

            newSelectedDomains.push(index);

            setSelectedDomains(newSelectedDomains);
        }
    };

    return (
        <tr onClick={() => onClickToSelect(index)} className={className}>
            <th scope="row" className="text-center">{domain["DomainName"]}</th>
            <td className="text-center">{domain["DomainSize"]}</td>
            <td className="text-center">[{domain["IssueValues"].toString()}]</td>
            <td className="text-center">{domain["Opposition"]}</td>
            <td className="text-center">{parseFloat(domain["BalanceScore"]) === 0 ? 0 : domain["BalanceScore"]}</td>
        </tr>
    );
};


const DomainsPage = ({setPage}) => {
    const [domains, setDomains] = useState(null);
    const [selectedDomains, setSelectedDomains] = useState([]);

    const [sortType, setSortType] = useState([null, null]);

    const [disabledAll, setDisabledAll] = useState(false);
    const [disabledSubmit, setDisabledSubmit] = useState(true);

    const [errorModal, setErrorModal] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    const configDomains = useSelector(state => state.config.domains);

    const dispatch = useDispatch();

    const handleSubmit = () => {
        if (!disabledSubmit && selectedDomains.length > 0) {
            setDisabledAll(true);
            setDisabledSubmit(true);

            dispatch(updateDomains(selectedDomains));

            setPage("LOGGERS");
        } else {
            setErrorMessage("You must select at least one domain.");
            setErrorModal(true);
        }

    };

    const goBack = () => {
        if (!disabledAll) {
            setDisabledAll(true);
            setDisabledSubmit(true);

            dispatch(updateDomains(selectedDomains));

            setPage("AGENTS");
        }
    };

    const onClickToCreate = () => {
        if (!disabledAll) {
            setDisabledAll(true);
            setDisabledSubmit(true);

            dispatch(updateDomains(selectedDomains));

            setPage("CREATE_DOMAINS");
        }
    };

    const onClickToClearAll = () => {
        if (!disabledAll) {
            setDisabledAll(true);
            setDisabledSubmit(true);

            setSelectedDomains([]);

            setDisabledAll(false);
        }
    };

    const onClickToSort = (colName) => {
        if (sortType[0] === colName) {
            if (sortType[1] === 'DESC')
                setSortType([colName, 'ASC']);
            else
                setSortType([colName, 'DESC']);
        } else {
            setSortType([colName, 'ASC']);
        }
    };

    useEffect(() => {
        setSelectedDomains(configDomains);
    }, [configDomains]);

    useEffect(() => {
        setDisabledSubmit(domains !== null && domains.length === 0);
    }, [domains]);

    useEffect(() => {
        if (domains === null) {
            fetchDomains().then((response) => {
                if (response.error) {
                    setErrorModal(true);
                    setErrorMessage(response.errorMessage);
                } else {
                    setDomains(response["domains"]);
                }
            });
        }
    }, [domains]);

    useEffect(() => {
        if (sortType[0] !== null && sortType[1] !== null) {
            let sortedDomains = [...domains];

            if (sortType[0] !== 'IssueValues')
                sortedDomains.sort((a, b) => (a[sortType[0]] < b[sortType[0]] ? -1 : 1));
            else
                sortedDomains.sort((a, b) => (a["IssueValues"].length < b["IssueValues"].length ? -1 : 1));

            if (sortType[1] === 'DESC') {
                sortedDomains.reverse();
            }

            let sortedSelectedDomains = [];

            for (let i in selectedDomains) {
                const domain = domains[selectedDomains[i]];

                sortedSelectedDomains.push(sortedDomains.indexOf(domain));
            }

            setDomains(sortedDomains);
            setSelectedDomains(sortedSelectedDomains);
        }
    }, [sortType]);

    return (
        <>
            <div className="container my-2 py-2">
                <div className="row my-2 mx-1 table-responsive" style={{height: "85vh"}}>
                    <table className="table table-striped table-hover border border-1 shadow-sm">
                        <thead className="sticky-top">
                        <tr>
                            <th scope="col" onClick={() => onClickToSort("DomainName")} className="text-center">Domain Name {sortType[0] === 'DomainName' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("DomainSize")} className="text-center">Domain Size {sortType[0] === 'DomainSize' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("IssueValues")} className="text-center">Issue Values {sortType[0] === 'IssueValues' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("Opposition")} className="text-center">Opposition {sortType[0] === 'Opposition' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("BalanceScore")} className="text-center">Balance Score {sortType[0] === 'BalanceScore' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                        </tr>
                        </thead>
                        <tbody>
                        {domains === null && <h3 className="m-1"><b>Loading...</b></h3>}
                        {domains !== null && domains.length === 0 && <b className="m-1">No domain exist.</b>}
                        {domains !== null && domains.length > 0 && domains.map((domain, index) => (
                            <DomainRow domain={domain} index={index} selectedDomains={selectedDomains}
                                       setSelectedDomains={setSelectedDomains} key={index}/>
                        ))}
                        </tbody>
                    </table>
                </div>
            </div>
            <div className="row my-2 mx-1 bg-secondary p-1">
                <div className="d-flex justify-content-between">
                    <button className="btn btn-light rounded" onClick={goBack} disabled={disabledAll}>Back
                    </button>
                    <button className={selectedDomains.length > 0 ? "btn btn-danger rounded" : "btn btn-secondary border border-light rounded"} disabled={disabledAll || selectedDomains.length === 0} onClick={onClickToClearAll}>{selectedDomains.length === 0 ? "No Selection" : "Unselect " + selectedDomains.length + " Domains"}</button>
                    <button className="btn btn-success rounded" disabled={disabledAll} onClick={onClickToCreate}>Create Domain</button>
                    <button className="btn btn-primary rounded" onClick={handleSubmit} disabled={disabledSubmit}>Next
                    </button>
                </div>
            </div>
            {errorModal && <InfoModal modalType={MODAL_ERROR} setModal={setErrorModal} message={errorMessage}/>}
        </>

    );
};

export default DomainsPage;
