import {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {updateSettings} from "../../../actions/config";
import axios from "axios";
import InfoModal, {MODAL_ERROR, MODAL_SUCCESS} from "../../modals/InfoModal";
import checkForType, {CheckMessage, getCheckStyle, IntegerType, StringType} from "../../domain/DomainGeneratorCheck";
import {SERVER_ADDRESS, CLIENT_ADDRESS} from "../../../config";


const submit = async (config) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/create/tournament_configuration",
            JSON.stringify({config: config}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const TournamentSettingsPage = ({setPage}) => {

    const [name, setName] = useState("");

    const [checkRound, setCheckRound] = useState(false);
    const [round, setRound] = useState(0);

    const [checkTime, setCheckTime] = useState(false);
    const [time, setTime] = useState(0);

    const [selfNegotiation, setSelfNegotiation] = useState(true);

    const [repeat, setRepeat] = useState(1);

    const [resultsDir, setResultsDir] = useState("results\\");
    const [checkResultsDir, setCheckResultsDir] = useState(false);

    const [seed, setSeed] = useState(1234);

    const [shuffle, setShuffle] = useState(false);

    const [drawingFormat, setDrawingFormat] = useState("matplotlib-PNG");

    const [disabledAll, setDisabledAll] = useState(false);
    const [disabledSubmit, setDisabledSubmit] = useState(true);

    const [errorModal, setErrorModal] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    const [successModal, setSuccessModal] = useState(false);
    const [successMessage, setSuccessMessage] = useState("");

    const [checkList, setCheckList] = useState({});

    const [submitted, setSubmitted] = useState(false);

    const config = useSelector(state => state.config);

    const dispatch = useDispatch();

    const handleSubmit = async () => {
        if (!disabledAll && !disabledSubmit) {
            setDisabledAll(true);
            setDisabledSubmit(true);

            dispatch(updateSettings({
                name: name,
                deadline_time: checkTime ? parseInt(time) : null,
                deadline_round: checkRound ? parseInt(round) : null,
                self_negotiation: selfNegotiation,
                repeat: parseInt(repeat),
                result_dir: resultsDir,
                seed: parseInt(seed),
                shuffle: shuffle,
                drawing_format: drawingFormat
            }));

            setSubmitted(true);
        }
    };

    const goBack = () => {
        if (!disabledAll) {
            setDisabledAll(true);
            setDisabledSubmit(true);

            dispatch(updateSettings({
                name: name,
                deadline_time: checkTime ? time : null,
                deadline_round: checkRound ? round : null,
                self_negotiation: selfNegotiation,
                repeat: repeat,
                result_dir: resultsDir,
                seed: seed,
                shuffle: shuffle,
                drawing_format: drawingFormat
            }));

            if (config.loggers.includes("OppModelLoggers"))
                setPage("OPPONENT_MODELS");
            else
                setPage("LOGGERS");
        }

    };

    const closeSuccessModal = () => {
        setSuccessModal(false);
        setPage("MAIN");
    };

    useEffect(() => {
        if (checkRound) {
            if (round === null) {
                setRound(0);
                checkList["Round"] = 'Value must be a positive value (number of rounds).';
            }
        } else {
            if (round !== null) {
                checkList["Round"] = '';
                setRound(null);
            }
        }
    }, [checkRound]);

    useEffect(() => {
        if (checkTime) {
            if (time === null) {
                setTime(0);
                checkList["Time"] = 'Value must be a positive value (seconds).';
            }
        } else {
            if (time !== null) {
                checkList["Time"] = '';
                setTime(null);
            }
        }
    }, [checkTime]);

    useEffect(() => {
        if (!checkTime && !checkRound) {
            setDisabledSubmit(true);
        } else {
            for (const [key, value] of Object.entries(checkList)) {
                if (value !== '') {
                    setDisabledSubmit(true);
                    return;
                }
            }

            if ((checkTime && (time === '' || time === null)) || (checkRound && (round === '' || round === null)) || repeat === '' || name === '' || resultsDir === '' || seed === '') {
                setDisabledSubmit(true);
            } else {
                setDisabledSubmit(false);
            }
        }
    }, [checkTime, time, checkRound, round, repeat, name, resultsDir, seed, checkList]);

    useEffect(() => {
        setName(config.name);
        setTime(config.deadline_time);
        setCheckTime(config.deadline_time !== null);
        setRound(config.deadline_round);
        setCheckRound(config.deadline_round !== null);
        setSelfNegotiation(config.self_negotiation);
        setRepeat(config.repeat);
        setResultsDir(config.result_dir);
        setShuffle(config.shuffle);
        setDrawingFormat(config.drawing_format);
    }, [config]);

    useEffect(() => {
        if (submitted) {
            let loggers = config.loggers;

            if (loggers.includes("DefaultLoggers")) {
                loggers.splice(loggers.indexOf("DefaultLoggers"), 1);

                loggers.push("MoveAnalyzeLogger");
                loggers.push("BidSpaceLogger");
                loggers.push("TournamentSummaryLogger");
            }

            if (loggers.includes("GraphLoggers")) {
                loggers.splice(loggers.indexOf("GraphLoggers"), 1);

                loggers.push("FinalGraphsLogger");
                loggers.push("DomainGraphsLogger");
                loggers.push("UtilityDistributionLogger");
            }

            if (loggers.includes("OppModelLoggers")) {
                loggers.splice(loggers.indexOf("OppModelLoggers"), 1);

                loggers.push("EstimatedUtilityLogger");
                loggers.push("EstimatedMoveLogger");
                loggers.push("EstimatedBidSpaceLogger");
            }

            let modifiedConfig = {...config};

            modifiedConfig["loggers"] = loggers;

            submit(modifiedConfig).then((response) => {
                if (response.error) {
                    setErrorMessage(response.errorMessage);
                    setErrorModal(true);
                } else {
                    setSuccessMessage(response.message);
                    setSuccessModal(true);
                }

                setSubmitted(false);
                setDisabledAll(false);
                setName("");
            });
        }
    }, [submitted]);

    useEffect(() => {
        if (!checkResultsDir) {
            if (name !== '') {
                setResultsDir('results\\' + name + "\\");
            } else {
                setResultsDir('results\\');
            }

            checkList["ResultDir"] = '';
        }
    }, [checkResultsDir, resultsDir, name]);

    return (
        <div className="col">
            <div className="row">
                <div className="row">
                    <div className="input-group my-3">
                        <div className="col-2 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text" id="label_name">Name</span>
                            </div>
                        </div>
                        <div className="col-8 mx-3">
                            <input type="text" className={getCheckStyle(checkList, "Name for YAML file")} placeholder="tournament_example"
                                   aria-label="Name" aria-describedby="label_name" value={name}
                                   onInput={(e) => checkForType(e, setName, checkList, setCheckList, "Name", StringType)}
                                   disabled={disabledAll}/>

                            <CheckMessage name="Name" checkList={checkList} />
                        </div>

                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-3">
                        <div className="col-2 d-flex">
                            <input type="checkbox" className="form-check-input mx-2 mt-2 border-2" checked={checkRound}
                                   onChange={(e) => setCheckRound(!checkRound)}/>
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text" id="label_round">Round-Based Deadline</span>
                            </div>
                        </div>
                        <div className="col-8 mx-3">
                            <input type="text" className="form-control" placeholder="Number of rounds"
                                   aria-label="Round" aria-describedby="label_round" value={round}
                                   onInput={(e) => checkForType(e, setRound, checkList, setCheckList, "Round", IntegerType)}
                                   disabled={disabledAll || !checkRound}/>

                            {checkRound && <CheckMessage name="Round" checkList={checkList} />}
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-3">
                        <div className="col-2 d-flex">
                            <input type="checkbox" className="form-check-input mx-2 mt-2 border-2" checked={checkTime}
                                   onChange={(e) => setCheckTime(!checkTime)}/>
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text" id="label_time">Time-Based Deadline</span>
                            </div>
                        </div>
                        <div className="col-8 mx-3">
                            <input type="text" className="form-control" placeholder="Seconds"
                                   aria-label="Time" aria-describedby="label_time" value={time}
                                   onInput={(e) => checkForType(e, setTime, checkList, setCheckList, "Time", IntegerType)}
                                   disabled={disabledAll || !checkTime}/>

                            {checkTime && <CheckMessage name="Time" checkList={checkList} />}
                        </div>

                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-3">
                        <div className="col-2 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text" id="label_self_negotiation">Self-Negotiation</span>
                            </div>
                        </div>
                        <div className="col-8 mx-3">
                            <select className="form-select" value={selfNegotiation} disabled={disabledAll}
                                    onChange={(e) => setSelfNegotiation(e.target.value)} aria-label="SelfNegotiation"
                                    aria-describedby="label_self_negotiation">
                                <option value={true}>Yes</option>
                                <option value={false}>No</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-3">
                        <div className="col-2 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text" id="label_repeat">Session Repeat</span>
                            </div>
                        </div>
                        <div className="col-8 mx-3">
                            <input type="text" className="form-control" placeholder="Number of repetition of a session"
                                   aria-label="Repeat" aria-describedby="label_repeat" value={repeat}
                                   onInput={(e) => checkForType(e, setRepeat, checkList, setCheckList, "Repeat", IntegerType)}
                                   disabled={disabledAll}/>

                            <CheckMessage name="Repeat" checkList={checkList} />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-3">
                        <div className="col-2 d-flex">
                            <input type="checkbox" className="form-check-input mx-2 mt-2 border-2" checked={checkResultsDir}
                                   onChange={(e) => setCheckResultsDir(!checkResultsDir)}/>
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text" id="label_results_dir">Directory for Results</span>
                            </div>
                        </div>
                        <div className="col-8 mx-3">
                            <input type="text" className="form-control" placeholder="results/"
                                   aria-label="ResultsDir" aria-describedby="label_results_dir" value={resultsDir}
                                   onInput={(e) => checkForType(e, setResultsDir, checkList, setCheckList, "ResultDir", StringType)}
                                   disabled={disabledAll || !checkResultsDir}/>

                            {checkResultsDir && <CheckMessage name="ResultDir" checkList={checkList} />}
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-3">
                        <div className="col-2 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text" id="label_shuffle">Shuffle</span>
                            </div>
                        </div>
                        <div className="col-8 mx-3">
                            <select className="form-select" value={shuffle} onChange={(e) => setShuffle(e.target.value)}
                                    aria-label="Shuffle" aria-describedby="label_shuffle" disabled={disabledAll}>
                                <option value={true}>Yes</option>
                                <option value={false}>No</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-3">
                        <div className="col-2 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text" id="label_seed">Random Seed</span>
                            </div>
                        </div>
                        <div className="col-8 mx-3">
                            <input type="text" className="form-control" placeholder="tournament_example"
                                   aria-label="Seed" aria-describedby="label_seed" value={seed}
                                   onInput={(e) => checkForType(e, setSeed, checkList, setCheckList, "Seed", IntegerType)}
                                   disabled={disabledAll}/>

                            <CheckMessage name="Seed" checkList={checkList} />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group my-3">
                        <div className="col-2 d-flex">
                            <div className="input-group-prepend w-100">
                                <span className="input-group-text" id="label_drawing_format">Graph Format</span>
                            </div>
                        </div>
                        <div className="col-8 mx-3">
                            <select className="form-select" value={drawingFormat}
                                    onChange={(e) => setDrawingFormat(e.target.value)} aria-label="DrawingFormat"
                                    aria-describedby="label_drawing_format" disabled={disabledAll}>
                                <option value={"matplotlib-PNG"}>PNG with matplotlib</option>
                                <option value={"matplotlib-SVG"}>SVG with matplotlib</option>
                                <option value={"plotly"}>HTML with plotly</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row my-2 mx-1 bg-secondary p-1">
                <div className="d-flex justify-content-between">
                    <button className="btn btn-light rounded" onClick={goBack} disabled={disabledAll}>Back
                    </button>
                    <button className="btn btn-primary rounded" onClick={handleSubmit} disabled={disabledSubmit}>Create
                    </button>
                </div>
            </div>

            {errorModal && <InfoModal modalType={MODAL_ERROR} setModal={setErrorModal} message={errorMessage}/>}
            {successModal && <InfoModal modalType={MODAL_SUCCESS} setModal={setSuccessModal} message={successMessage}
                                        closeHandler={closeSuccessModal}/>}
        </div>
    );
};


export default TournamentSettingsPage;
