import {useEffect, useState} from "react";
import axios from "axios";
import InfoModal, {MODAL_ERROR, MODAL_INFO, MODAL_SUCCESS} from "../modals/InfoModal";
import {SERVER_ADDRESS, CLIENT_ADDRESS} from "../../config";


const fetchTournaments = async () => {
    try {
        const response = await axios.get(SERVER_ADDRESS + "/fetch/tournaments",
            {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const killTournament = async (selectedTournament) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/remove/tournament",
            JSON.stringify({path: selectedTournament}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


const openFolder = async (path) => {
    try {
        const response = await axios.post(SERVER_ADDRESS + "/open/folder",
            JSON.stringify({path: path}), {
            headers: {'Content-Type': 'application/json', 'Access-Control-Allow-Origin': CLIENT_ADDRESS}
            });

        return response.data;
    } catch (e) {
        return {"error": true, "errorMessage": e}
    }
};


export const TournamentRow = ({tournament, selectedTournament, setSelectedTournament, setErrorModal, setErrorMessage}) => {
    const [statusClass, setStatusClass] = useState("badge rounded-pill");
    const [className, setClassName] = useState("");

    useEffect(() => {
        if (selectedTournament === tournament["name"]) {
            setClassName("table-primary");
        } else {
            setClassName("");
        }
    }, [selectedTournament, tournament]);

    useEffect(() => {
        if (tournament["status"] === 'Active')
            setStatusClass("badge rounded-pill bg-success");
        else
            setStatusClass("badge rounded-pill bg-secondary");
    }, [tournament]);


    const onClickToSelect = () => {
        if (selectedTournament === tournament["name"]) {
            setSelectedTournament(null);
        } else {
            setSelectedTournament(tournament["name"]);
        }
    };

    const onClickToOpen = async () => {
        const response = await openFolder(tournament["full_result_dir"]);

        if (response.error) {
            setErrorMessage(response.errorMessage);
            setErrorModal(true);
        }
    };

    return (
        <tr onClick={onClickToSelect} className={className}>
            <th scope="row" className="text-center">{tournament["name"]}</th>
            <td className="text-center"><span className={statusClass}>{tournament["status"]}</span></td>
            <td className="text-center">{tournament["completed_percentage"]}</td>
            <td className="text-center">{tournament["estimated_remaining_time"]}</td>
            <td className="text-center">{tournament["current"]}</td>
            <td className="text-center">{tournament["start_time"]}</td>
            <td className="text-center">{tournament["elapsed_time"]}</td>
            <td className="text-center"><button className="text-primary text-decoration-underline" onClick={onClickToOpen}>{tournament["result_dir"]}</button></td>
            <td className="text-center">{tournament["last_update"]}</td>
        </tr>
    );
};



const MonitorTournamentsPage = ({setPage}) => {
    const [tournaments, setTournaments] = useState(null);
    const [selectedTournament, setSelectedTournament] = useState(null);

    const [sortType, setSortType] = useState([null, null]);

    const [disabledAll, setDisabledAll] = useState(false);

    const [errorModal, setErrorModal] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");

    const [successModal, setSuccessModal] = useState(false);
    const [successMessage, setSuccessMessage] = useState("");

    const [removeModal, setRemoveModal] = useState(false);
    const [removeModalMessage, setRemoveModalMessage] = useState("");

    useEffect(() => {
        const interval = setInterval(() => {
            fetchTournaments().then((response) => {
                if (response.error) {
                    setErrorMessage(response.errorMessage);
                    setErrorModal(true);
                } else {
                    let sortedTournaments = [...response["tournaments"]];

                    if (sortType[0] !== null && sortType[1] !== null) {
                        sortedTournaments.sort((a, b) => (a[sortType[0]] < b[sortType[0]] ? -1 : 1));

                        if (sortType[1] === 'DESC') {
                            sortedTournaments.reverse();
                        }
                    } else {
                        sortedTournaments.sort((a, b) => (a["name"] < b["name"] ? -1 : 1));
                        sortedTournaments.sort((a, b) => (a["status"] < b["status"] ? -1 : 1));
                    }

                    setTournaments(sortedTournaments);
                }
            });
        }, 1000);

        return () => clearInterval(interval);
    });

    useEffect(() => {
        if (sortType[0] !== null && sortType[1] !== null) {
            let sortedTournaments = [...tournaments];

            sortedTournaments.sort((a, b) => (a[sortType[0]] < b[sortType[0]] ? -1 : 1));

            if (sortType[1] === 'DESC') {
                sortedTournaments.reverse();
            }

            setTournaments(sortedTournaments);
        }
    }, [sortType]);

    const onClickToSort = (colName) => {
        if (sortType[0] === colName) {
            if (sortType[1] === 'DESC')
                setSortType([colName, 'ASC']);
            else
                setSortType([colName, 'DESC']);
        } else {
            setSortType([colName, 'ASC']);
        }
    };

    const goBack = () => {
        setPage("MAIN");
    };

    const onClickToRemove = () => {
        if (!disabledAll && selectedTournament !== null) {
            setRemoveModal(true);
            setRemoveModalMessage("Are you sure to stop/remove the tournament process?");
        }
    };

    const removeTournament = async () => {
        if (!disabledAll && selectedTournament !== null) {
            setRemoveModal(false);
            setDisabledAll(true);

            const response = await killTournament(selectedTournament);

            if (response.error) {
                setErrorMessage(response.errorMessage);
                setErrorModal(response.error);
            } else {
                setSuccessMessage("Selected tournament process is successfully stopped/removed.");

                setSuccessModal(true);

                setSelectedTournament(null);
                setTournaments(null);
            }

            setDisabledAll(false);
        }
    };

    return (
        <>
            <div className="container my-2 py-2">
                <div className="row my-2 mx-1 p-3 overflow-scroll" style={{height: "85vh"}}>
                    <table className="table table-striped table-hover border border-1 shadow-sm">
                        <thead>
                        <tr>
                            <th scope="col" onClick={() => onClickToSort("name")}
                                className="text-center">Name {sortType[0] === 'name' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("status")}
                                className="text-center">Status {sortType[0] === 'status' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("completed_percentage")}
                                className="text-center">Completed
                                Percentage {sortType[0] === 'completed_percentage' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("estimated_remaining_time")}
                                className="text-center">Estimated Remaining
                                Time {sortType[0] === 'estimated_remaining_time' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("current")}
                                className="text-center">Current {sortType[0] === 'current' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("start_time")}
                                className="text-center">Start
                                Time {sortType[0] === 'start_time' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("elapsed_time")}
                                className="text-center">Elapsed
                                Time {sortType[0] === 'elapsed_time' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("result_dir")}
                                className="text-center">Result
                                Directory {sortType[0] === 'result_dir' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                            <th scope="col" onClick={() => onClickToSort("last_update")} className="text-center">Last
                                Update {sortType[0] === 'last_update' && (sortType[1] === 'ASC' ? "\u21D1" : "\u21D3")}</th>
                        </tr>
                        </thead>
                        <tbody>
                        {tournaments === null && <h3 className="m-1"><b>Loading...</b></h3>}
                        {tournaments !== null && tournaments.length === 0 && <b className="m-1">No tournament exist.</b>}
                        {tournaments !== null && tournaments.length > 0 && tournaments.map((tournament, index) => (
                            <TournamentRow tournament={tournament} selectedTournament={selectedTournament} setSelectedTournament={setSelectedTournament} setErrorModal={setErrorModal} setErrorMessage={setErrorMessage} key={index}/>
                        ))}
                        </tbody>
                    </table>
                </div>
            </div>
            <div className="row my-2 mx-1 bg-secondary p-1">
                <div className={selectedTournament === null ? "d-flex justify-content-start" : "d-flex justify-content-between"}>
                    <button className="btn btn-light rounded" onClick={goBack} disabled={disabledAll}>Home Page
                    </button>
                    {selectedTournament !== null && <button className="btn btn-danger rounded" disabled={disabledAll} onClick={onClickToRemove}>Remove</button> }
                </div>
            </div>
            {errorModal && <InfoModal modalType={MODAL_ERROR} setModal={setErrorModal} message={errorMessage}/>}
            {successModal && <InfoModal modalType={MODAL_SUCCESS} setModal={setSuccessModal} message={successMessage} />}
            {removeModal &&
                <InfoModal modalType={MODAL_INFO} setModal={setRemoveModal} message={removeModalMessage} buttons={[{
                    "className": "btn btn-danger rounded",
                    "disabled": false,
                    "onClick": removeTournament,
                    "name": "Remove"
                }]}/>}
        </>
    );
};

export default MonitorTournamentsPage;
