

const MainPage = ({setPage}) => {
    return (
        <>
            <div className="col p-3">
                <div className="row my-1 mx-1 py-5 px-3 position-relative border border-1 rounded shadow-sm">
                    <div className="position-absolute top-0 start-0 m-1">
                        <b>Tournament</b>
                    </div>
                    <div className="d-flex justify-content-between m-5">
                        <div className="col d-flex justify-content-center">
                            <button className="btn btn-outline-secondary shadow-sm" onClick={() => setPage("AGENTS")}>Create
                                Tournament
                            </button>
                        </div>
                        <div className="col d-flex justify-content-center">
                            <button className="btn btn-outline-secondary shadow-sm" onClick={() => setPage("RUN_TOURNAMENTS")}>Run
                                Tournament
                            </button>
                        </div>
                        <div className="col d-flex justify-content-center">
                            <button className="btn btn-outline-secondary shadow-sm"
                                    onClick={() => setPage("MONITOR_TOURNAMENTS")}>Monitor
                                Tournament
                            </button>
                        </div>
                    </div>
                </div>
                <div className="row my-1 mx-1 py-5 px-3 position-relative border border-1 rounded shadow-sm">
                    <div className="position-absolute top-0 start-0 m-1">
                        <b>Domain Generator</b>
                    </div>
                    <div className="d-flex justify-content-between m-5">
                        <div className="col d-flex justify-content-center">
                            <button className="btn btn-outline-secondary shadow-sm"
                                    onClick={() => setPage("CREATE_DOMAINS")}>Create Automated
                            </button>
                        </div>
                        <div className="col d-flex justify-content-center">
                            <button className="btn btn-outline-secondary shadow-sm"
                                    onClick={() => setPage("CREATE_DOMAIN")}>Create Manually
                            </button>
                        </div>
                        <div className="col d-flex justify-content-center">
                            <button className="btn btn-outline-secondary shadow-sm"
                                    onClick={() => setPage("DISPLAY_DOMAINS")}>Show Domains
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};


export default MainPage;
